// request-reducer.js
//  名刺の登録先候補の一覧
// @flow
import { Map } from 'immutable'
import {
  FETCH_SVCONFIG_SUCCESS,
  FETCH_USER_SUCCESS,
  // CHANGE_QUERY_SETTINGS,
  SELECT_RECORDTYPE,
  FETCH_NAMECARD_SUCCESS,
  FETCH_ACCOUNTOPTIONS, FETCH_ACCOUNTOPTIONS_SUCCESS, FETCH_ACCOUNTOPTIONS_FAILURE,
  FETCH_CONTACTOPTIONS, FETCH_CONTACTOPTIONS_SUCCESS, FETCH_CONTACTOPTIONS_FAILURE,
  SET_REGIST_TARGET, SET_TARGET_OWNER,
  SET_ACCOUNT_PARAM, SET_CONTACT_PARAM, SET_TODO_PARAM,
  SET_LEAD_PARAM, SET_CAMPAIGN_PARAM, // lx 2.0
  SET_RECORD_OWNER,
  SET_OVERWRITE,
  // SET_RECORDTYPE,
  DO_REGIST, DO_REGIST_SUCCESS,
  CLEAR_RESPONSE,
  // SET_ALLOWRETRY,
} from '../actions/index';

const INITIAL_STATE = Map({
  target: {
    pType: "0",       // 1: Account系、2: Lead系
    cardId: null,
    acntId: null,
    acntName: null,
    contId: null,
    contName: null,
    leadId: null,
    leadName: null,
    ownerId: null,
    ownerName: null,
  },
  owner: {
    choice: -1,      // -1: 未指定、0: 実行中ユーザ、1: 名刺所有者、2:その他のユーザ選択
    user: null,
  },
  acntParam: {
    overwrite : true,
    recTypeId : null,
    recTypeName : null,
    allowSave : false,
  },
  contParam: {
    overwrite : true,
    recTypeId : null,
    recTypeName : null,
    allowSave : false,
  },
  leadParam: {    // lx 2.0
    overwrite : true,
    recTypeId : null,
    recTypeName : null,
    allowSave : false,
  },
  campaignParam: {
    campaignId: null,
    campaignName: null,
    statusObj: null,
  },
  todoParam: {
    createTodo: true,
    subject : null,
    activityDate : new Date(),
    comment : null,
  },
  allowRetry: false,
  response: null,
});

export default (state: Object = INITIAL_STATE, action: Object) => {
  // let label;
  // let selectedOwner;
  switch(action.type) {

  case FETCH_SVCONFIG_SUCCESS:
    return state.update((items) => {
      for (let [key, item] of items) {
        if (key === "acntParam" && item.overwrite ) {
          item.overwrite = action.payload.enableAccountEdit && !action.payload.accountNotOverWrite;   // 編集可能 かつ カスタム設定上書きしないオフ
        }
        else if (key === "contParam" && item.overwrite ) {
          item.overwrite = action.payload.enableContactEdit && !action.payload.contactNotOverWrite;   // 編集可能 かつ カスタム設定上書きしないオフ
        }
        else if (key === "leadParam" && item.overwrite ) { // lx 2.0
          // 基本 v2.0.2 で、SB_NameCard_S1Config に enableLeadEdit を追加したので、undefinedなら  leadNotOverWrite だけから判断します。
          if (typeof action.payload.enableLeadEdit !== "undefined") {
            item.overwrite = action.payload.enableLeadEdit && !action.payload.leadNotOverWrite;   // 編集可能 かつ カスタム設定上書きしないオフ
          }
          else {
            item.overwrite = !action.payload.leadNotOverWrite;   // 編集可能 かつ カスタム設定上書きしないオフ
          }
        }
        else if (key === "todoParam") {
          item.createTodo = action.payload.createTodo;     // TODO関連の設定
          item.activityDate = action.payload.activityDate || new Date();
          item.subject  = action.payload .todoSubject;
        }
        // else if (key === "owner") {
        //   item.user = { id: action.payload.userId, name: action.payload.userName };     // デフォルトの所有者
        // }
        items.set(key, item);
      };
      return items;
    });

  // case FETCH_USER_SUCCESS:
  //   return state.update("owner", (item) => {
  //     item.user = { id: action.payload.Id, name: action.payload.Name };
  //     return item;
  //   });

  // 選択した名刺の情報
  case FETCH_NAMECARD_SUCCESS:
    let card = action.payload;
    // return state.update("target", (item) => {
    //     item = Object.assign({}, ...item, { cardId: card.id, cardName: card.name, ownerId: card.ownerId, ownerName: card.ownerName, });
    //     return item;
    return state.update((items) => {
      for (let [key, item] of items) {
        // if (key === "acntParam" && item.overwrite) {
        //   item.overwrite = !card.isNotOverwrite; // 上書き不可 ? でみる (card.accountId != null);    // アカウントへの参照があったら、上書きするは初期値オン  なんか変だけど。既存の仕様
        // }
        // else if (key === "contParam" && item.overwrite) {
        //   item.overwrite = !card.isNotOverContactwrite; // 上書き不可 ? でみる (card.contactId != null);    // コンタクトへの参照があったら、上書きするは初期値オン
        // }
        // else
        if (key === "todoParam" && card.card_exchange_date) {
          item.activityDate = new Date(card.card_exchange_date);      // ToDo の 予定日
        }
        else if (key === "target") {
          item.cardId = card.id;
          item.cardName = card.name;
          item.ownerId = card.ownerId;
          item.ownerName = card.ownerName;
          // item = Object.assign({}, ...item, { cardId: card.id, cardName: card.name, ownerId: card.ownerId, ownerName: card.ownerName, });
        }
        // else if (key === "owner") {
        //   item.user = { id: action.payload.userId, name: action.payload.userName };     // デフォルトの所有者
        // }
        items.set(key, item);
      };
      return items;
    });

  case SET_REGIST_TARGET:
    return state.update("target", (item) => {
      // Object.keys(action.payload).forEach((k) => {
      //   item[k] = action.payload[k];
      // });
      item = Object.assign({}, item, action.payload);
      return item;
    });

  case SET_RECORD_OWNER:
  {
    let selectedOwner = action.payload;
    return state.update("owner", (item) => {
      item.choice = action.payload.choice;
      item.user = action.payload.user ? action.payload.user : item.user;
      return item;
    });
  }
  case SET_ACCOUNT_PARAM:
    return state.update("acntParam", (item) => {
      // Object.keys(action.payload).forEach((k) => {
      //   item[k] = action.payload[k];
      // });
      item = Object.assign({}, item, action.payload);
      return item;
    });

  case SET_CONTACT_PARAM:
    return state.update("contParam", (item) => {
      item = Object.assign({}, item, action.payload);
      return item;
    });

  case SET_TODO_PARAM:
    return state.update("todoParam", (item) => {
      item = Object.assign({}, item, action.payload);
      return item;
    });

  case SET_LEAD_PARAM:  // lx 2.0
    return state.update("leadParam", (item) => {
      item = Object.assign({}, item, action.payload);
      return item;
    });

  case SET_CAMPAIGN_PARAM: // lx 2.0
    return state.update("campaignParam", (item) => {
      item = Object.assign({}, item, action.payload);
      return item;
    });

  case SET_OVERWRITE:
  {
    let label;
    if (action.payload.sobjectname === "Account") {
      label = "acntParam";
    }
    else if (action.payload.sobjectname === "Contact") {
      label = "contParam";
    }
    else {  // lx 2.0
      label = "leadParam";
    }
    return state.update(label, (item) => {
      item.overwrite = action.payload.val;
      return item;
    });
  }
  // 登録完了
  case DO_REGIST_SUCCESS:
    return state.update("response", (item) => {
      // Object.keys(action.payload).forEach((k) => {
      //   item[k] = action.payload[k];
      // });
      item = Object.assign({}, item, action.payload);
      return item;
    });

  // 保持している SFDC からのレスポンスをクリアする。
  case CLEAR_RESPONSE:
    return state.merge(Map({ response: null }));

  // //  重複エラーで再試行可能なとき
  // case SET_ALLOWRETRY:
  //   return state.update((value) => {
  //     for (let [label, item] of value) {
  //       if (label === "acntParam") {
  //         item.allowSave = action.payload;   // 強制保存設定
  //       }
  //       else if (label === "contParam") {
  //         item.allowSave = action.payload;   // 強制保存設定
  //       }
  //       else if (label === "allowRetry") {
  //         item = action.payload;
  //       }
  //       value.set(label, item);
  //     };
  //     return value;
  //   });


  default:
    return state;
  }
}



