import React, { Component } from 'react';
import { Link } from 'react-router';
import classnames from 'classnames';
import Footer from './Footer';
import RegistOption from '../containers/RegistOptionContainer';
import {
  Form,
  Table,
  TableHeader,
  TableBody,
  TableRow,
  TableHeaderColumn,
  TableRowColumn,
  TableRowColumnActions,
  DropdownButton,
  MenuItem,
  RadioGroup, Radio,
  Select, Option,
  Popover,
  CheckboxGroup, Checkbox,
} from '../rlds';

class RegistContact extends Component {
  constructor(props) {
    super(props);
    console.log("location:");
    console.log(this.props.location);
  }

  componentWillMount() {
    if (this.props.location.query.prev === '/') {
      // if (this.props.accountId) {
        this.props.fetchContactOptions(this.props.accountId, this.props.querySettings);
      // }
      // else {
      //   this.props.buildCreateNewOption();
      // }
    }
  }

  componentDidMount() {
    this.props.setCurrentStep(1);
  }

  // renderCategories(categories) {
  //  return categories.map((c) => {
  //     c = c.trim();
  //     return (
  //       <Link to={"filter/" + c} key={c} className="list-group-item-text">{" " + c + " "}</Link>
  //     );
  //  });
  // }

  // renderCards(cards) {
  //   return cards.map((card) => {
  //     return (
  //       <li className="list-group-item" key={card.Id}>
  //         <Link style={{color:'black'}} to={'/cards/' + card.Id}>
  //           <h3 className="list-group-item-heading">{card.Name}</h3>
  //         </Link>
  //           {this.renderCategories(card.company_name__c ? card.company_name__c.split(',') : [])}
  //       </li>
  //     );
  //   });
  // }


  // linkHandler(property) {
  //   window.location.hash = "#property/" + property.property_id;
  // }

  // actionHandler(data, value, label) {
  //   if (label === "Delete") {
  //     this.props.onDelete(data);
  //   } else if (label === "Edit") {
  //     this.props.onEdit(data);
  //   }
  // }

  onFieldChange(name, e, value) {
    this.setState({ [name]: value });
    if (name === "contOverwrite") {
      this.props.toggleOverwrite("Contact", e.target.checked);
    }
  }

  //
  selectOption(name, e, value) {
    this.props.selectOption(name, value);
    this.popoverDoSelect.toggle(false);
    // this.setState({selectedIndex: name});
    // this.props.changeOption(this.state.selectedIndex, name);
    // this.setState({selectedIndex: name});
    // this.props.accountOptions.forEach((item, idx) => {
    //   item.selected = item.index === name;
    // });
  }

  // goBack() {
  //   this.props.goBack();
  // }


  getRecTypeName(id)  {
    let name = null;
    this.props.recTypes.infos.forEach((item) => {
      if(item.id === id) {
        name = item.label;
      }
    });
    return name;
  }

  goOn(event) {
    event.preventDefault();
    let opt = null;
    this.props.registOptions.map((item) => {
      if (item.selected) {
        opt = item;
      }
    });
    if (!opt || (opt.type ===2 && !opt.lookuped)) {
      // 未選択
      this.popoverDoSelect.toggle(true);
      return
    }
    else {
      this.popoverDoSelect.toggle(false);
    }
    // this.props.setSelectedTarget(opt);
    // this.props.setRegistParam(opt);
    // // [this.props.selectedIndex];
    // let param = {
    //   targetId : opt.type === 1 ? opt.id : (opt.type === 2 ? opt.lookuped.id : null),
    //   name: opt.type === 1 ? opt.title : (opt.type === 2 ? opt.lookuped.title : null),
    //   // overwrite : true,
    //   recTypeId : opt.type === 3 && opt.recTypeId ? opt.recTypeId : null,
    //   recTypeName : opt.type === 3 && opt.recTypeName ? opt.recTypeName : null,
    //   allowSave : false,
    // }
    this.props.goOn(opt);
  }

  render() {
    if (this.props.loading || !this.props.registOptions) {
      return <div></div>
    }
    // 各オブジェクトのラベル名 2017.08.xx V1.20.2
    const namecardLabel = this.props.svconfig.namecardLabel;
    const accountLabel = this.props.svconfig.accountLabel;
    const contactLabel = this.props.svconfig.contactLabel;

    let opts;
    if (this.props.registOptions) {
      opts = this.props.registOptions.map((item, idx) => {
        return (
          <li className="slds-item" key={idx}>
            <RegistOption key={ idx } accountId={ this.props.accountId } sobjectname={ this.props.sobjectname } registOption={ item } recTypes={ this.props.recTypes }
              recTypeIds={ this.props.querySettings.acntRecTypeIds }
              dispRectype={ this.props.svconfig.registDispContactRectype } selectHandler={ this.selectOption.bind(this, idx) } />
          </li>
        );
      });
    }
    const required = true;
    const overwriteSecsion = this.props.svconfig.registDispContactOverWriteCheckbox ? classnames("slds-grid slds-grid--vertical-stretch") : classnames("slds-hide");  // 2017.06.xx v1.20, 2018.03.xx v1.22

    const styles = { padding: '12px' };
    return (
      <div>
        {/* ToDo: 未選択でOKしたときのPopupエラー */}
        <Popover key='12'
          ref={ ref => (this.popoverDoSelect = ref) }
          position='bottom-left'
          tooltip
          theme='error'
          style={{
            position: 'absolute',
            marginLeft: +4,
            marginTop: -30,
          }}>
          <div className="slds-text-body--regular">選択してください</div>
        </Popover>
        <div style={ styles } className="slds-scrollable">
          {/* 選択肢 */}
          <RadioGroup label='' name='radiogroup1'>
          <ul className="slds-has-dividers--bottom-space">
            {opts}
          </ul>
          </RadioGroup>
          {/* 上書き？ */}
          <Form>
            <div className={ overwriteSecsion }>
              <CheckboxGroup label='' >
                <Checkbox label={`既存の${contactLabel}の情報を${namecardLabel}情報で上書きする`} value="2" checked={ this.props.registParam.overwrite }
                    disabled={ !this.props.svconfig.enableContactEdit }
                    onChange={ this.onFieldChange.bind(this, "contOverwrite") } />
              </CheckboxGroup>
            </div>
          </Form>
          {/* フッターの分だけスペースをおく */}
          <div style={{ marginBottom: '48px' }}></div>
          {/* フッター */}
          <Footer left={{ label:"戻る", icon: "back", func: this.props.goBack }}
                  center={{ label:"Cancel", icon: "ban", func: this.props.cancel}}
                  right={{ label: "次へ", icon: "forward", func: this.goOn.bind(this) }} />
        </div>
      </div>
    );
  }

}


export default RegistContact;



