// ModalQuerySettings.js
import React, { Component, PropTypes } from 'react';

import {
  Modal,
  Form,
  Input,
  DateInput,
  Picklist,
  PicklistItem,
  FieldSet,
  Button,
  Lookup,
  Checkbox, CheckboxGroup,
  Icon,
} from '../rlds';

const { Row } = FieldSet;
const { Header, Content, Footer } = Modal;

export default class ModalQuerySettings extends Component {
  constructor(props) {
    super(props);
    this.state = {
      AccountRecTypes: this.props.querySettings.acntRecTypeIds,
      ContactRecTypes: this.props.querySettings.contRecTypeIds,
      LeadRecTypes: this.props.querySettings.leadRecTypeIds,
    };
  }

  save(event) {
    let acntRecTypeIds = this.state.AccountRecTypes;
    let contRecTypeIds = this.state.ContactRecTypes;
    let leadRecTypeIds = this.state.LeadRecTypes;
    // this.props.save({ acntRecTypeIds, contRecTypeIds });
    // 2018.02.xx v1.22 OK したら再検索する
    let querySettings = this.props.querySettings;
    querySettings.acntRecTypeIds = acntRecTypeIds;
    querySettings.contRecTypeIds = contRecTypeIds;
    querySettings.leadRecTypeIds = leadRecTypeIds;
    if (querySettings.bulkRegist) {
      // 一括登録のとき
      this.props.fetchRegisters(querySettings);
    }
    else if (this.props.step === 0) {
      // 取引先選択画面
      this.props.fetchAccountOptions(querySettings);
    }
    else if (this.props.step === 1 && this.props.accountId) {
      // 取引先責任者選択画面 取引先を選んでるとき
      this.props.fetchContactOptions(this.props.accountId, querySettings);
    }
    this.props.hideModal();
  }

  colseModal(event) {
    if (event) {
      event.preventDefault();
    }
    // this.setState({
    //   AccountRecTypes: this.props.querySettings.acntRecTypeIds,
    //   ContactRecTypes: this.props.querySettings.contRecTypeIds,
    // });
    this.props.hideModal();
  }
  // showModal(name) {
  //   this.setState({
  //     [name]: { ...this.state[name], opened: true },
  //   });
  // }

  // hideModal() {
  //   this.setState({
  //     opened: false,
  //   });
  // }
  onFieldChange(name, e, value) {
    this.setState({ [name]: value });
  }

  getRectypeSelecter(recTypes, selectedIds, disabled) {
    if (!recTypes.infos || recTypes.infos.length === 0) {
      return null;
    }
    let opts = recTypes.infos.map((info, idx) => {
      let b = selectedIds.length === 0 || selectedIds.indexOf(info.id) !== -1;
      return <Checkbox key={idx} label={ info.label } value={ info.id } checked={ b } disabled={ disabled } />;
    });
    let result =
        <CheckboxGroup label={ `${recTypes.objLabel}のレコードタイプ` } name= { `${recTypes.objName}RecTypes` }
                onChange={ this.onFieldChange.bind(this, `${recTypes.objName}RecTypes`) }
                disabled={ disabled } >
        {opts}
        </CheckboxGroup>;
    return result;
  }

  render() {
    // アカウントのレコードタイプ
    let acntRecTypeSelector;
    if (this.props.recTypes.Account) {
      acntRecTypeSelector = this.getRectypeSelecter(this.props.recTypes.Account, this.state.AccountRecTypes, this.props.step !== 0);
    }
    // コンタクトのレコードタイプ
    let contRecTypeSelector;
    if (this.props.recTypes.Contact) {
      contRecTypeSelector = this.getRectypeSelecter(this.props.recTypes.Contact, this.state.ContactRecTypes, (this.props.step !== 0 && this.props.step !== 1));
    }

    // コンタクトのレコードタイプ
    let leadRecTypeSelector;
    if (this.props.recTypes.Lead) {
      leadRecTypeSelector = this.getRectypeSelecter(this.props.recTypes.Lead, this.state.LeadRecTypes, (this.props.step !== 0 && this.props.step !== 0));
    }

    return (
      <div>
        <Modal
          opened={ this.props.opened }
          onHide={ this.colseModal.bind(this) }
        >
          <Header title='設定' closeButton/>
          <Content style={ { padding: '1em' } }>
            <Form type='compound'>
              <div className="slds-text-body--regular">検索の対象にするレコードタイプを指定します。</div>
              <div className="slds-m-left--large">
                <Row>
                  <FieldSet hidden={ this.props.querySettings.registTarget === 1 } >
                    { leadRecTypeSelector }
                  </FieldSet>
                  <FieldSet hidden={ this.props.querySettings.registTarget === 2 } >
                    { acntRecTypeSelector }
                  </FieldSet>
                  <FieldSet hidden={ this.props.querySettings.registTarget === 2 } >
                    { contRecTypeSelector }
                  </FieldSet>
                </Row>
              </div>
            </Form>
          </Content>
          <Footer directional>
            <Button type='neutral' label='Cancel' onClick={ this.colseModal.bind(this) } />
            <Button type='brand' label='OK' onClick={ this.save.bind(this) } />
          </Footer>
        </Modal>
      </div>
    );
  }
}

ModalQuerySettings.propTypes = {
  querySettings: PropTypes.shape({
    bulkRegist: PropTypes.bool,
    queryType: PropTypes.number,
    querySite: PropTypes.bool,
    queryState: PropTypes.bool,
    queryCity: PropTypes.bool,
    queryZip: PropTypes.bool,
    queryPersonEMail: PropTypes.bool,
    queryPersonName: PropTypes.bool,
    acntRecTypeIds: PropTypes.arrayOf(PropTypes.object),
    contRecTypeIds: PropTypes.arrayOf(PropTypes.object),
    leadRecTypeIds: PropTypes.arrayOf(PropTypes.object),
  }),
  // save: PropTypes.func,
  fetchRegisters: PropTypes.func.isRequired,
  fetchAccountOptions: PropTypes.func.isRequired,
  fetchContactOptions: PropTypes.func.isRequired,
  hideModal: PropTypes.func,
  recTypes: PropTypes.object,
  opened: PropTypes.bool,
};



