// Footer.js
import React, { Component, PropTypes } from 'react';
import { Link } from 'react-router';
import classnames from 'classnames';
import { Button } from '../rlds';//'react-lightning-design-system';

class Footer extends Component {
  constructor(props) {
    super(props);
  }

  render() {
    // ボタンをかこうDivのクラス
    let leftDiv = classnames("slds-container--left", !this.props.left ? "slds-hidden" : null);    // hide だと、center がずれてしまう?
    let centerDiv = classnames("slds-container--center", !this.props.center ? "slds-hidden" : null);
    let rightDiv = classnames("slds-container--right", !this.props.right ? "slds-hidden" : null);
    // Type
    let leftType = classnames(this.props.left && this.props.left.type ? this.props.left.type : "neutral");
    let centerType = classnames(this.props.center && this.props.center.type ? this.props.center.type : "neutral");
    let rightType = classnames(this.props.right && this.props.right.type ? this.props.right.type : "brand");
    // faアイコン
    // let leftIcon = classnames("fa", this.props.left.icon, "fa-lg");
    // let centerIcon = classnames("fa", this.props.center.icon, "fa-lg");
    // let rightIcon = classnames("fa", this.props.right.icon, "fa-lg");
    // <i className={ leftIcon } aria-hidden="true"></i>&nbsp;{this.props.left ? this.props.left.label : null}

    return(
      <div>
        <div className="slds-docked-form-footer slds-p-horizontal--x-large">
          {/* Left */}
          <div className={ leftDiv }>
            <Button type={ leftType } size='small' className='slds-float--left'
                  icon={ this.props.left && this.props.left.icon ? this.props.left.icon : null } iconAlign='left'
                  onClick={ this.props.left ? this.props.left.func : null }>
              {this.props.left ? this.props.left.label : null}
            </Button>
          </div>
          {/* Center */}
          <div className={ centerDiv }>
            <Button type={ centerType } size='small' className='slds-float--center'
              icon={ this.props.center && this.props.center.icon ? this.props.center.icon : null } iconAlign='left' onClick={ this.props.center ? this.props.center.func : null }>
              {this.props.center ? this.props.center.label : null}
            </Button>
          </div>
          {/* Right */}
          <div className={ rightDiv }>
            <Button type={ rightType } size='small' className='slds-float--right'
              icon={ this.props.right && this.props.right.icon ? this.props.right.icon : null } iconAlign='right' onClick={  this.props.right ? this.props.right.func : null }>
              {this.props.right ? this.props.right.label : null}
            </Button>
          </div>
        </div>
      </div>
    );
  }
}

Footer.propTypes = {
  left: PropTypes.shape({
    label: PropTypes.string.isRequired,
    icon: PropTypes.string,
    type: PropTypes.string,
    func: PropTypes.func.isRequired,
  }),
  center: PropTypes.shape({
    label: PropTypes.string.isRequired,
    icon: PropTypes.string,
    type: PropTypes.string,
    func: PropTypes.func.isRequired,
  }),
  right: PropTypes.shape({
    label: PropTypes.string.isRequired,
    icon: PropTypes.string,
    type: PropTypes.string,
    func: PropTypes.func.isRequired,
  }).isRequired,
};

export default Footer;



