// RegistHeader.js
//  lx 2.0 2018.07.xx リードまたは取引先... に変更
import React, { Component, PropTypes } from 'react';
import { Link } from 'react-router';
import classnames from 'classnames';
import {
  Spinner,
  PageHeader,
  PageHeaderHeading,
  PageHeaderHeadingTitle,
  PageHeaderDetail,
  PageHeaderDetailItem,
  PageHeaderDetailBody,
  PageHeaderDetailLabel,
  Icon, Crumb, Button, ButtonGroup, DropdownButton, MenuItem,
  Text, Grid,
} from '../rlds';
import ModalQuerySettings from '../containers/ModalQuerySettingsContainer';
import ModalNotification from '../containers/ModalNotificationContainer';

class RegistHeader extends Component {
  constructor(props) {
    super(props);
    this.state = {
      showQuerySettings: false,
    };
  }

  openQuerySetting() {
    this.setState({ showQuerySettings: true });
  }

  componentWillMount() {
    // this.props.fetchViewConfig();
    this.props.fetchSVConfig();
    // レコードタイプを取得
    var objNames = ['Account', 'Contact', 'Lead'];    // lx 2.0 Lead 追加
    this.props.fetchRecordTypeInfos(objNames);
  }
  // componentWillMount() {
  //   this.props.fetchViews();
  // }

  fetchLeadOptions(name, e) {
    // this.props.fetchAccountOptions(Number(name));
    // 2018.03.xx v.1.22  container メソッドを呼ぶ前にこちらでオプションパラメータ作成
    let querySettings;
    querySettings = this.props.querySettings;
    switch(Number(name)) {
      // 氏名
      case 1:
        querySettings.queryLeadType = 1;
        querySettings.queryPersonEMail = false;
        querySettings.queryPersonName = true;
        break;
      default: // 0
        querySettings.queryLeadType = 0;
        querySettings.queryPersonEMail = true;
        querySettings.queryPersonName = false;
        break;
    }
    // querySettings.acntRecTypeIds = this.props.querySettings.acntRecTypeIds;
    // querySettings.contRecTypeIds = this.props.querySettings.contRecTypeIds;
    // querySettings.leadRecTypeIds = this.props.querySettings.leadRecTypeIds;  // lx 2.0
    this.props.fetchAccountOptions(querySettings);
  }

  // 登録先候補の選択 0: 両方 1:アカウント 2:リード
  changeRegistTarget(name, e) { // lx 2.0
    let querySettings;
    querySettings = this.props.querySettings;
    querySettings.registTarget = Number(name);
    this.props.fetchAccountOptions(querySettings);
  }

  //  検索条件
  fetchAccountOptions(name, e) {
    // this.props.fetchAccountOptions(Number(name));
    // 2018.03.xx v.1.22  container メソッドを呼ぶ前にこちらでオプションパラメータ作成
    let querySettings;
    querySettings = this.props.querySettings;
    switch(Number(name)) {
      case 1:
        querySettings.queryType = 1;
        querySettings.querySite = true;
        querySettings.queryState = false
        querySettings.queryCity = false;
        querySettings.queryZip = false;
        break;
      case 2:
        querySettings.queryType = 2;
        querySettings.querySite = true;
        querySettings.queryState = true
        querySettings.queryCity = false;
        querySettings.queryZip = false;
        break;
      case 3:
        querySettings.queryType = 3;
        querySettings.querySite = true;
        querySettings.queryState = true
        querySettings.queryCity = true;
        querySettings.queryZip = false;
        break;
      case 4:
        querySettings.queryType = 4;
        querySettings.querySite = true;
        querySettings.queryState = false
        querySettings.queryCity = false;
        querySettings.queryZip = true;
        break;
      default: // 0
        querySettings.queryType = 0;
        querySettings.querySite = false;
        querySettings.queryState = false
        querySettings.queryCity = false;
        querySettings.queryZip = false;
        break;
    }
    // querySettings.acntRecTypeIds = this.props.querySettings.acntRecTypeIds;
    // querySettings.contRecTypeIds = this.props.querySettings.contRecTypeIds;
    // querySettings.leadRecTypeIds = this.props.querySettings.leadRecTypeIds;  // lx 2.0
    this.props.fetchAccountOptions(querySettings);
  }

  render() {

    // let   = classnames(this.props.loading ? "slds-show" : "slds-hide");
    if (!this.props.svconfig) {
      return <div></div>
    }

    // 名前空間名
    const namecardPrefix = this.props.svconfig.namecardPrefix;
    // 各オブジェクトのラベル名 2017.08.xx V1.20.2
    const namecardLabel = this.props.svconfig.namecardLabel;
    const accountLabel = this.props.svconfig.accountLabel;
    const contactLabel = this.props.svconfig.contactLabel;
    const accountLabels = this.props.svconfig.labels.account;    // lx 2.0
    const leadLabel = this.props.svconfig.labels.lead.objName;    // lx 2.0
    const leadLabels = this.props.svconfig.labels.lead;    // lx 2.0
    //  検索方法
    const { registTarget, queryType, queryLeadType } = this.props.querySettings;
    //  検索条件項目の 表示・非表示用クラスの値
    const condtionLabel = registTarget === 0 ? classnames("") : classnames("slds-hide");
    const condtionLead = registTarget === 1 ? classnames("slds-hide") : classnames("");
    const condtionAccount = registTarget === 2 ? classnames("slds-hide") : classnames("");
    return(
      <div>
        <PageHeader>
          <PageHeaderHeading
            breadCrumbs={[
              <Crumb key={0} href={`/${namecardPrefix}/o`}>{namecardLabel}</Crumb>,
            ]}
            title={`${leadLabel}または${accountLabel}・${contactLabel}に登録`}
            figure={
              <Icon
                category="standard"
                icon="account"
                size="large"
              />
            }
            info=""
            rightActions={[
              <DropdownButton key={0} type="icon-more" icon="standard_objects" menuAlign="right" >
                {/* 登録の対象 */}
                <MenuItem selected={registTarget===2} iconRight={registTarget===2 ? "check" : ""} onClick={this.changeRegistTarget.bind(this, 2)} disabled={this.props.step !==0}>
                  <i className="fa fa-user" style={{color: "#6495ed"}} aria-hidden="true"></i>&nbsp;{ leadLabel }
                </MenuItem>
                <MenuItem selected={registTarget===1} iconRight={registTarget===1 ? "check" : ""} onClick={this.changeRegistTarget.bind(this, 1)} disabled={this.props.step !==0}>
                  <i className="fa fa-building-o" style={{color: "#6495ed"}} aria-hidden="true"></i>&nbsp;{ accountLabel }・{ contactLabel }
                </MenuItem>
                <MenuItem selected={registTarget===0} iconRight={registTarget===0 ? "check" : ""} onClick={this.changeRegistTarget.bind(this, 0)} disabled={this.props.step !==0}>
                  <i className="fa fa-users" style={{color: "#6495ed"}} aria-hidden="true"></i>&nbsp;両方
                </MenuItem>
              </DropdownButton>,
              <DropdownButton key={1} type="icon-more" icon="search" menuAlign="right" >
              {/* 検索の条件項目の選択 cornflowerblue #6495ed */}
                {/* lx 2.0 リード の条件追加 */}
                <MenuItem className={ condtionLabel }>{ leadLabel }</MenuItem>
                <MenuItem className={ condtionLead } selected={queryLeadType===0} iconRight={queryLeadType===0 ? "check" : ""} onClick={this.fetchLeadOptions.bind(this, 0)} disabled={this.props.step !==0}>
                  <i className="fa fa-envelope-open-o" style={{color: "#6495ed"}}></i>&nbsp;{leadLabels.email}
                </MenuItem>
                <MenuItem className={ condtionLead } selected={queryLeadType===1} iconRight={queryLeadType===1 ? "check" : ""} onClick={this.fetchLeadOptions.bind(this, 1)} disabled={this.props.step !==0}>
                  <i className="fa fa-user" style={{color: "#6495ed"}} aria-hidden="true"></i>&nbsp;{leadLabels.name}
                </MenuItem>

                <MenuItem className={ condtionLabel } divider="top">{ accountLabel }</MenuItem>
                <MenuItem className={ condtionAccount } selected={queryType===0} iconRight={queryType===0 ? "check" : ""} onClick={this.fetchAccountOptions.bind(this, 0)} disabled={this.props.step !==0}>
                  <i className="fa fa-building-o" style={{color: "#6495ed"}} aria-hidden="true"></i>&nbsp;{accountLabels.name}
                </MenuItem>
                <MenuItem className={ condtionAccount } selected={queryType===1} iconRight={queryType===1 ? "check" : ""} onClick={this.fetchAccountOptions.bind(this, 1)} disabled={this.props.step !==0}>
                  <i className="fa fa-sitemap" style={{color: "#6495ed"}} aria-hidden="true"></i>&nbsp;{accountLabels.name}と{accountLabels.site}
                </MenuItem>
                <MenuItem className={ condtionAccount } selected={queryType===2} iconRight={queryType===2 ? "check" : ""} onClick={this.fetchAccountOptions.bind(this, 2)} disabled={this.props.step !==0}>
                  <i className="fa fa-map-signs" style={{color: "#6495ed"}}></i>&nbsp;{accountLabels.name}、{accountLabels.site}と{accountLabels.state}
                </MenuItem>
                <MenuItem className={ condtionAccount } selected={queryType===3} iconRight={queryType===3 ? "check" : ""} onClick={this.fetchAccountOptions.bind(this, 3)} disabled={this.props.step !==0}>
                  <i className="fa fa-location-arrow" style={{color: "#6495ed"}}></i>&nbsp;{accountLabels.name}、{accountLabels.site}と{accountLabels.city}まで
                </MenuItem>
                <MenuItem className={ condtionAccount } selected={queryType===4} iconRight={queryType===4 ? "check" : ""} onClick={this.fetchAccountOptions.bind(this, 4)} disabled={this.props.step !==0}>
                  <i className="fa fa-envelope-o" style={{color: "#6495ed"}}></i>&nbsp;{accountLabels.name}、{accountLabels.site}と{accountLabels.zipcode}
                </MenuItem>
              </DropdownButton>,
              <ButtonGroup key={2} className="slds-button-space-left">
                {/* 設定用 検索大将レコードタイプの絞り込みとか */}
                <Button type="icon-border" icon="filterList" onClick={ this.props.showModalQuerySettings.bind(this) } />
                {/*<Button type="icon-border" icon="filterList" />
                <DropdownButton type="icon-more" icon="sort" menuAlign="right">
                  <MenuItem>Menu Item #1</MenuItem>
                  <MenuItem>Menu Item #2</MenuItem>
                  <MenuItem>Menu Item #3</MenuItem>
                </DropdownButton>*/}
              </ButtonGroup>,
            ]}
          />
        </PageHeader>
        {/* 設定モーダル */}
        <ModalQuerySettings />
        {/* 通知モーダル */}
        <ModalNotification />
      </div>
    );
  }
}

ModalQuerySettings.propTypes = {
  querySettings: PropTypes.shape({
    bulkRegist: PropTypes.bool,
    queryType: PropTypes.number,
    querySite: PropTypes.bool,
    queryState: PropTypes.bool,
    queryCity: PropTypes.bool,
    queryZip: PropTypes.bool,
    acntRecTypeIds: PropTypes.arrayOf(PropTypes.object),
    contRecTypeIds: PropTypes.arrayOf(PropTypes.object),
  }),
  fetchAccountOptions: PropTypes.func.isRequired,
};

export default RegistHeader


