// RegistProcess.js
//  lx 2.0 2018.07.xx 第1ステップを リード,取引先選択 に変更
import React, { Component, PropTypes } from 'react';
import classnames from 'classnames';
import { Link } from 'react-router'

class RegistProcess extends Component {

  // getCurrentPath() {
  //   switch(this.props.step) {
  //   case 1:
  //     return 'cont';
  //   case 2:
  //     return 'confrim';
  //   case 3:
  //     return 'fin';
  //   default: // step 0
  //     return 'acnt';
  //   }
  // }

  // 先のページへは遷移しない
  // リードで更新のときは第2ステップに遷移しない
  //  第2ステップだけなので、next はつかわなくてもいいけど
  goto(next, event) {
    if (next >= this.props.step ||
      (this.props.location.query.proc === "lead" && this.props.location.query.ope === "update")) {
      event.preventDefault();
    }
  }

  render() {
    if (!this.props.svconfig) {
      return <div></div>
    }

    // 各オブジェクトのラベル名 2017.08.xx V1.20.2
    const accountLabel = this.props.svconfig.accountLabel;
    const contactLabel = this.props.svconfig.contactLabel;
    const leadLabel = this.props.svconfig.labels.lead.objName;    // lx 2.0
    const campaignLabel = this.props.svconfig.labels.campaign.objName;    // lx 2.0
    // ToDo登録  2017.08.xx V1.20.2
    const todoLabelCreate = this.props.svconfig.todoLabelCreate;

    let percent = String((this.props.step * 100) / 2) + '%';

    let step2class = classnames("slds-wizard__item", this.props.step >=1 ? "slds-is-active" : null);
    let step2page = null;
    if (this.props.location && this.props.location.pathname!=='/') {
      if (this.props.location.query.proc !== "lead") {
        step2page = "/cont";
      }
      else {
        step2page = "/camp";
      }
    }
    let query = Object.assign({}, this.props.location.query, { prev: this.props.location.pathname });
    let step3class = classnames("slds-wizard__item", this.props.step >=2 ? "slds-is-active" : null);
    // let prev = this.getCurrentPath();
    return (
      <div className="slds-grid slds-grid--align-center slds-p-top--xx-small">
        <div className="slds-col slds-size--10-of-12">
          <div className="slds-wizard" role="navigation">
            <ol className="slds-wizard__list">
              <li className="slds-wizard__item slds-is-active">
                <Link to={{ pathname: "/", query }} className="slds-wizard__link">
                  <span className="slds-wizard__marker"></span>
                  <span className="slds-wizard__label slds-text-title slds-truncate" title="Navigation">{leadLabel}・{accountLabel}選択</span>
                </Link>
              </li>
              <li className={ step2class }>
                <Link to={{ pathname: step2page, query }} onClick={ this.goto.bind(this, 1) } className="slds-wizard__link">
                  <span className="slds-wizard__marker"></span>
                  <span className="slds-wizard__label slds-text-title slds-truncate" title="Actions">{campaignLabel}/{contactLabel}選択</span>
                </Link>
              </li>
              <li className={ step3class }>
                <a href="javascript:void(0);" className="slds-wizard__link">
                  <span className="slds-wizard__marker"></span>
                  <span className="slds-wizard__label slds-text-title slds-truncate" title="Compact Layout">確認・{todoLabelCreate}</span>
                </a>
              </li>
            </ol>
            <span className="slds-wizard__progress">
              <span className="slds-wizard__progress-bar" style={{ width: percent, }}></span>
            </span>
          </div>
        </div>
      </div>
    );
  }
}

RegistProcess.propTypes = {
  step: PropTypes.number.isRequired,
};

export default RegistProcess


