// config-reducer.js
//  名刺ごとの登録情報の一覧
// @flow
import { Map } from 'immutable'
import {
  FETCH_SVCONFIG, FETCH_SVCONFIG_SUCCESS, FETCH_SVCONFIG_FAILURE,
  FETCH_RECORDTYPES, FETCH_RECORDTYPES_SUCCESS, FETCH_RECORDTYPES_FAILURE,
  FETCH_USER, FETCH_USER_SUCCESS, FETCH_USER_FAILURE,
  CHANGE_QUERY_SETTINGS,
  FETCH_ACCOUNTOPTIONS_SUCCESS, // lx 2.0
} from '../actions/index';


const INITIAL_STATE = Map({
  svconfig: null,
  recTypes: {},
  user: null,
  querySettings: {
    registTarget: -1,      // 登録先候補の表示状態 0: 両方、1: アカウント、2:リード
    bulkRegist: false,
    queryType: 0,
    queryLeadType: 0,
    querySite: false,
    queryState: false,
    queryCity: false,
    queryZip: false,
    queryPersonEMail: true,
    queryPersonName: false,
    acntRecTypeIds: [],
    contRecTypeIds: [],
    leadRecTypeIds: [],
  },
  error: null,
  loading: false,
});

export default (state: Object = INITIAL_STATE, action: Object) => {
  switch(action.type) {
  // SmartViscaの設定
  case FETCH_SVCONFIG:
    return state.merge(Map({ svconfig: null, error: null, loading: true }));
  case FETCH_SVCONFIG_SUCCESS:
    return state.merge(Map({ svconfig: action.payload, error:null, loading: false }));
  case FETCH_SVCONFIG_FAILURE:
    return state.merge(Map({ svconfig: null, error: action.payload, loading: false }));

  // レコードタイプ取得
  case FETCH_RECORDTYPES:
    return state.merge(Map({ recTypes: {}, error: null, loading: true }));
  case FETCH_RECORDTYPES_SUCCESS:
    return state.merge(Map({ recTypes: action.payload, error:null, loading: false }));
  case FETCH_RECORDTYPES_FAILURE:
    return state.merge(Map({ recTypes: {}, error: action.payload, loading: false }));

  // 実行中ユーザ
  case FETCH_USER:
    return state.merge(Map({ user: null, error: null, loading: true }));
  case FETCH_USER_SUCCESS:
    return state.merge(Map({ user: action.payload, error:null, loading: false }));
  case FETCH_USER_FAILURE:
    return state.merge(Map({ user: null, error: action.payload, loading: false }));

  // 候補の検索設定
  // 検索対象のレコードタイプを選択•解除
  case CHANGE_QUERY_SETTINGS:
    return state.update("querySettings", (item) => {
      // Object.keys(action.payload).forEach((k) => {
      //   item[k] = action.payload[k];
      // });
      item = Object.assign({}, item, action.payload);
      return item;
    });

  case FETCH_ACCOUNTOPTIONS_SUCCESS:
  {
    let registTarget = 0;
    let acnt = false;
    let lead = false;
    action.payload.forEach((item, idx) => {
      if (!acnt && item.otype === 'Account') {
        acnt = true;
      }
      if (!lead && item.otype === 'Lead') {
        lead = true;
      }
    });
    if (acnt) {
      registTarget = lead ? 0 : 1;
    }
    else if (lead) {
      registTarget = 2;
    }
    //  querySettings の registTargetを更新
    return state.update("querySettings", (item) => {
      item = Object.assign({}, item, { registTarget });
      return item;
    });
  }

  default:
    return state;
  }
}



