// 登録実行
export const doRegistAccountContact = (req) => {
  return {
    type: DO_REGIST,
    payload: registAccountContact(req),
  };
}

export const doRegistAccountContactSuccess = (res) => {
  return {
    type: DO_REGIST_SUCCESS,
    payload: res,
  };
}

export const doRegistAccountContactFailure = (error) => {
  return {
    type: DO_REGIST_FAILURE,
    payload: error,
  };
}

