export const fetchCards = (opt) => {
  return {
    type: FETCH_CARDS,
    payload: getFilteredRecords(opt),
  };
}

export const fetchCardsSuccess = (cards) => {
  return {
    type: FETCH_CARDS_SUCCESS,
    payload: cards,
  };
}

export const fetchCardsFailure = (error) => {
  return {
    type: FETCH_CARDS_FAILURE,
    payload: error,
  };
}

