// 登録先の選択
export const selectOption = (rowIdx, optIdx) => {
  return {
    type: SELECT_OPTION,
    payload: { row: rowIdx, option: optIdx },
  };
}

// ToDOo 登録 反転
export const toggleTodo = (rowIdx) => {
  return {
    type: TOGGLE_TODO,
    payload: { row: rowIdx },
  };
}

// ToDOo 登録
export const setAllTodo = (b) => {
  return {
    type: SET_ALLTODO,
    payload: b,
  };
}

export const selectRecordType = (objName, recTypeId) => {
  return {
    type: SELECT_RECORDTYPE,
    payload: { [objName]: recTypeId },
  };
}

export const selectOverwrite = (objName, value) => {
  return {
    type: SELECT_OVERWRITE,
    payload: { [objName]: value },
  };
}


// 登録先候補のオブジェクトの指定を変更
// type 0: BOTH 1:Account 2:Lead
export const changeRegistTarget = (type) => {
  return {
    type: CHANGE_REGISTTARGET,
    payload: type,
  };
}

// 検索設定 変更
// レコードタイプ指定も
export const changeQuerySettings = (querySettings) => {
  return {
    type: CHANGE_QUERY_SETTINGS,
    payload: querySettings,
  };
}



