// RegistProcessContainer.js
import React, { Component } from 'react';
import { connect } from 'react-redux';
import RegistProcess from '../components/RegistProcess.js';

const mapStateToProps = (state, ownProps) => {
  const { step, } = state.get('process').toObject();
  const svconfig = state.getIn(['config', 'svconfig']);   // 2017.08.xx V1.20.2
  // const location = ownProps.location;
  // if (ownProps.router) {
  //   location = ownProps.router.location
  // }
  return { step, svconfig, location: ownProps.location, };
}

const mapDispatchToProps = (dispatch, ownProps) => {
  return {
    //  クリックされて遷移
    goto(step, prev) {
      let query= {
        pType: ownProps.location.pType,
        // aid: ownProps.router.location.query.aid,
        // cid: ownProps.router.location.query.cid,
        // lid: ownProps.router.location.query.lid,
        // campId: ownProps.router.location.query.campId,
        prev,
      };
      let pathname = '/'  // Step 1
      if (step === 1) { // Step 2
        if (ownProps.location.query.pType === "2") {
          // キャンペーンの選択
          pathname = '/camp';
        }
        else {
          // リードの選択
          pathname = '/cont';
        }
      }
      // if (step === 0) {
      // }
      ownProps.router.push({
        pathname,
        query,
      });
    },
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(RegistProcess);




