// ModalNotification.js
import React, { Component, PropTypes } from 'react';
import classnames from 'classnames';
import {
  // Modal,
  // Form,
  // Input,
  // DateInput,
  // Picklist,
  // PicklistItem,
  // FieldSet,
  Button,
  // Lookup,
  // Checkbox, CheckboxGroup,
  Icon,
} from '../rlds';
import Collapsible from '../components/Collapsible.js';

// const { Row } = FieldSet;
// const { Header, Content, Footer } = Modal;

export default class ModalNotification extends Component {
  constructor(props) {
    super(props);
  }

  hideModal(event) {
    if (event) {
      event.preventDefault();
    }
    this.props.hideModal(this.props.errors[0].content.goback);
  }

  gotoRecord(recId, event) {
    event.preventDefault();
    this.props.goto(recId);
  }

  makeSfdcEventInfo(sfdcevent) {
    let lines = [];
    Object.keys(sfdcevent).forEach((k, idx) => {
      let val = JSON.stringify(sfdcevent[k]);
      lines.push(<p key={idx} className="slds-m-left--x-small">{k}:&nbsp;{val}</p>);
    });
    return <Collapsible title = {{ open: "詳細表示", close: "閉じる" }}>
      { lines }
    </Collapsible>;
  }

  makeApexResultInfo(result) {
    let lines = []
    let bulkMode = result.req.targets.length > 0;
    result.req.targets.forEach((target, row) => {
      let error = result.dbErrors[target.cardId];   // 名刺のレコードIDからエラー情報を取得
      if (error) {
        // エラーだったとき
        lines.push(<p key={row}>{ bulkMode ? `${row+1}行目: ` : "" }{error.text}</p>);
        if (error.duplicate && error.mapId2Name ) {
          let links = [];
          Object.keys(error.mapId2Name).forEach((k, idx) => {
            links.push(<li className="slds-item"><a key={{row}-{idx}} onClick={ this.gotoRecord.bind(this, k) } >{error.mapId2Name[k]}</a></li>);
          });
          lines.push(
            <div className="slds-p-left--small">
              <ul className="slds-list--horizontal slds-has-dividers--left">
                { links }
              </ul>
            </div>);
        }
      }
    });
    return <div className="slds-m-left--x-small">{ lines }</div>;
  }

  render() {

    if (!this.props.errors || !Array.isArray(this.props.errors) || this.props.errors.length === 0 || !this.props.errors[0].content) {
      return null;
    }

    if (this.props.errors[0].notify === false) {
      return null;
    }

    const content = this.props.errors[0].content;
    let detail;
    if (content.sfdcevent) {
      // JavaScript Remoting の エラー
      detail = this.makeSfdcEventInfo(content.sfdcevent);
    }
    else if (content.apexresult && content.apexresult.dbErrors) {
      // JavaScript Remoting の エラー
      detail = this.makeApexResultInfo(content.apexresult);
    }

    let modalClass = classnames(this.props.errors[0].notify ? "slds-show" : "slds-hide");
    const size = (content.message && content.message.length > 100) || (content.apexresult && content.apexresult.dbErrors) ? "large" : "small"; //
    const dialogClass = classnames("slds-modal", "slds-fade-in-open", `slds-modal--${size}`);
    const level = content.level;
    const headerClass = classnames("slds-notify", "slds-notify--toast", `slds-theme--${level}`);
    let title = content.title ? content.title : "Alert";
    let message;
    message = <div>
              <p>{ content ? content.message : "エラーがありました。"}</p>
              { detail }
            </div>;
    if (content && title == content.message ) {
      title = level;
    }
    return (
      <div className={ modalClass }>
        <div role="dialog" tabindex="-1" className={ dialogClass }>
          <div className="slds-modal__container">
            <div className="slds-modal__header">

              {/*<Button type='icon-inverse' icon='close'/>
              <Button className="slds-modal__close slds-button--icon-inverse" title="Close">
                <Icon category="utility" icon="close" size="large" textColor="white" className="slds-button__icon"/>
                <span className="slds-assistive-text">Close</span>
              </Button>*/}
              <div className="slds-notify_container">
                <div className={ headerClass } role="alert">
                  <span className="slds-assistive-text">Error</span>

                  {/*<Button type='icon-inverse' icon='close'/>
                  <Button className="slds-button slds-notify__close slds-button--icon-inverse" title="Close">
                    <Icon category="utility" icon="close" textColor="white" />
                    <span className="slds-assistive-text">Close</span>
                  </Button>*/}
                  <div className="slds-notify__content slds-grid">
                    <Icon category="utility" icon="warning" size="small" textColor="white" />
                    <div className="slds-col slds-align-middle">
                      <h2 className="slds-text-heading--small">{ title }</h2>
                    </div>
                  </div>
                </div>
              </div>
              <h2 className="slds-text-heading--medium">&nbsp;</h2>
            </div>
            <div className="slds-modal__content slds-p-around--medium">
              <div>
                { message }
              </div>
            </div>
            <div className="slds-modal__footer">
              <Button className="slds-button slds-button--neutral slds-container--center" onClick={ this.hideModal.bind(this) }>OK</Button>
            </div>
          </div>
        </div>
        <div className="slds-backdrop slds-backdrop--open"></div>
      </div>
    );
  }
}

ModalNotification.propTypes = {
  errors: PropTypes.array,
  errors: PropTypes.arrayOf(
    PropTypes.shape({
      notify: PropTypes.boolean,
      title: PropTypes.string,
      message: PropTypes.string,
      sfdcevent: PropTypes.object,
    })
  ),
};





