// ModalQuerySettingsContainer.js
import React, { Component } from 'react';
import { connect } from 'react-redux';
import { showModalQuerySettings, changeQuerySettings,
        fetchAccountOptions, fetchAccountOptionsSuccess, fetchAccountOptionsFailure,
        fetchContactOptions, fetchContactOptionsSuccess, fetchContactOptionsFailure,
        fetchRegisters, fetchRegistersSuccess, fetchRegistersFailure,
      } from '../actions/index';
import ModalQuerySettings from '../components/ModalQuerySettings.js';

const mapStateToProps = (state, ownProps) => {
  const request = state.get('request').toObject();
  const { step, } = state.get('process').toObject();
  const querySettings = state.getIn(['config', 'querySettings']);
  const config = state.get('config').toObject();
  const modalQuerySettings = state.getIn(['process', 'modalQuerySettings']);
  const res = { opened: modalQuerySettings,
      svconfig: config.svconfig,
      recTypes: config.recTypes,
      querySettings,
      accountId: request.target.acntId,
      step, };
  return  res;
}

const mapDispatchToProps = (dispatch, ownProps) => {
  return {

    //  クエリ設定モーダルの 　show - hide
    hideModal: () => {
      dispatch(showModalQuerySettings(false));
    },

    // // 設定保存
    // save: ({ acntRecTypeIds, contRecTypeIds }) => {
    //   // dispatch(changeQuerySettings({ acntRecTypeIds, contRecTypeIds }));
    //   let querySettings = {
    //     acntRecTypeIds: acntRecTypeIds,
    //     contRecTypeIds: contRecTypeIds
    //   };
    //   dispatch(changeQuerySettings({ querySettings }));
    // },

    // アカウント選択ページでの 候補抽出
    fetchAccountOptions: (querySettings) => {
      dispatch(changeQuerySettings({ querySettings })); // 2018.03.xx v.1.22
      dispatch(fetchAccountOptions({ cardId: vfValue.targetId, querySettings })).then((response) => {
        return !response.error ? dispatch(fetchAccountOptionsSuccess(response.payload)) : dispatch(fetchAccountOptionsFailure(response.payload));
      });
    },

    // コンタクト選択ページでの 候補抽出
    fetchContactOptions: (accountId, querySettings) => {
      dispatch(changeQuerySettings({ querySettings })); // 2018.03.xx v.1.22
      dispatch(fetchContactOptions({ cardId: vfValue.targetId, accountId, querySettings })).then((response) => {
        // let data = response.payload.data ? response.payload.data : 'Network Error';
        return !response.error ? dispatch(fetchContactOptionsSuccess(response.payload)) : dispatch(fetchContactOptionsFailure(response.payload));
      });
    },


    // 一括登録での 候補抽出
    fetchRegisters: (querySettings) => {
      dispatch(changeQuerySettings({ querySettings }));   // 2018.03.xx v.1.22
      dispatch(fetchRegisters({ cardIds: vfValue.targetIds, querySettings })).then((response) => {
        return !response.error ? dispatch(fetchRegistersSuccess(response.payload)) : dispatch(fetchRegistersFailure(response.payload));
      });
    },
  }
}


export default connect(mapStateToProps, mapDispatchToProps)(ModalQuerySettings);




