// RegistAccount.js
// 名刺と登録先のオプションのリスト
import React, { Component } from 'react';
import { Link } from 'react-router';
import classnames from 'classnames';
import Footer from './Footer';
import RegistOption from '../containers/RegistOptionContainer';
import {
  Form,
  Table,
  TableHeader,
  TableBody,
  TableRow,
  TableHeaderColumn,
  TableRowColumn,
  TableRowColumnActions,
  DropdownButton,
  MenuItem,
  RadioGroup, Radio,
  Select, Option,
  Popover,
  CheckboxGroup, Checkbox,
} from '../rlds';

class RegistAccount extends Component {
  constructor(props) {
    super(props);
  }

  componentWillMount() {
    // this.props.fetchRegisters(this.props.querySettings);
    if (!this.props.location.query.prev) {
      this.props.fetchAccountOptions(this.props.querySettings);
    }
  }

  componentDidMount() {
    this.props.setCurrentStep(0);
  }

  // renderCategories(categories) {
  //  return categories.map((c) => {
  //     c = c.trim();
  //     return (
  //       <Link to={"filter/" + c} key={c} className="list-group-item-text">{" " + c + " "}</Link>
  //     );
  //  });
  // }

  // renderCards(cards) {
  //   return cards.map((card) => {
  //     return (
  //       <li className="list-group-item" key={card.Id}>
  //         <Link style={{color:'black'}} to={'/cards/' + card.Id}>
  //           <h3 className="list-group-item-heading">{card.Name}</h3>
  //         </Link>
  //           {this.renderCategories(card.company_name__c ? card.company_name__c.split(',') : [])}
  //       </li>
  //     );
  //   });
  // }


  // linkHandler(property) {
  //   window.location.hash = "#property/" + property.property_id;
  // }

  // actionHandler(data, value, label) {
  //   if (label === "Delete") {
  //     this.props.onDelete(data);
  //   } else if (label === "Edit") {
  //     this.props.onEdit(data);
  //   }
  // }

  onFieldChange(name, e) {
    this.setState({ [name]: e.target.checked });
    if (name === "acntOverwrite") {
      this.props.toggleOverwrite("Account", e.target.checked);
    }
    else if (name === "leadOverwrite") {
      this.props.toggleOverwrite("Lead", e.target.checked);
    }
  }

  //
  selectOption(name, e, value) {
    this.props.selectOption(name, value);
    this.popoverDoSelect.toggle(false);
    // this.setState({selectedIndex: name});
    // this.props.changeOption(this.state.selectedIndex, name);
    // this.setState({selectedIndex: name});
    // this.props.accountOptions.forEach((item, idx) => {
    //   item.selected = item.index === name;
    // });
  }

  // goBack() {
  //   this.props.goBack();
  // }

  goOn(event) {
    event.preventDefault();
    let opt = null;
    this.props.registOptions.map((item) => {
      if (item.selected) {
        opt = item;
      }
    });
    if (!opt || (opt.type ===2 && !opt.lookuped)) {
      // 未選択
      this.popoverDoSelect.toggle(true);
      return
    }
    else {
      this.popoverDoSelect.toggle(false);
    }
    // this.props.setSelectedTarget(opt);
    // this.props.setRegistParam(opt);
    // // // [this.props.selectedIndex];
    // // let param = {
    // //   targetId : opt.type === 1 ? opt.id : (opt.type === 2 ? opt.lookuped.id : null),
    // //   name: opt.type === 1 ? opt.title : (opt.type === 2 ? opt.lookuped.title : null),
    // //   // overwrite : true,
    // //   recTypeId : opt.type === 3 && opt.recTypeId ? opt.recTypeId : null,
    // //   recTypeName : opt.type === 3 && opt.recTypeName ? opt.recTypeName : null,
    // //   allowSave : false,
    // // }
    this.props.goOn(opt);
  }

  render() {
    if (this.props.loading || !this.props.registOptions || !this.props.svconfig) {
      return <div></div>;
    }
    // 各オブジェクトのラベル名 2017.08.xx V1.20.2
    const namecardLabel = this.props.svconfig.namecardLabel;
    const accountLabel = this.props.svconfig.accountLabel;
    const contactLabel = this.props.svconfig.contactLabel;
    const leadLabel = this.props.svconfig.labels.lead.objName;    // lx 2.0

    let opts;
    if (this.props.registOptions) {
      opts = this.props.registOptions.map((item, idx) => {
        if (item.otype === "Account") {
          return (
            <li className="slds-item" key={idx}>
              <RegistOption key={ idx } sobjectname="Account" registOption={ item } recTypes={ this.props.recTypes["Account"] }
                recTypeIds={ this.props.querySettings.acntRecTypeIds }
                dispRectype={ this.props.svconfig.registDispAccountRectype } selectHandler={ this.selectOption.bind(this, idx) } />
            </li>
          );
        }
        else {
          return (
            <li className="slds-item" key={idx}>
              <RegistOption key={ idx } sobjectname="Lead" registOption={ item } recTypes={ this.props.recTypes["Lead"] }
                recTypeIds={ this.props.querySettings.acntRecTypeIds }
                dispRectype={ this.props.svconfig.registDispLeadRectype } selectHandler={ this.selectOption.bind(this, idx) } />
            </li>
          );
        }
      });
    }
    const required = true;
    // 表示・非表示
    const overwriteSecsion = this.props.svconfig.registDispAccountOverWriteCheckbox || this.props.svconfig.registDispLeadOverWriteCheckbox ?
          classnames("slds-grid slds-grid--vertical-stretch") : classnames("slds-hide");  // 2017.06.xx v1.20, 2018.03.xx v1.22 2018.08.xx lx 2.0
    const acntOverwriteCB = this.props.svconfig.registDispAccountOverWriteCheckbox && this.props.querySettings.registTarget !== 2 ?
          classnames("slds-grid slds-grid--vertical-stretch") : classnames("slds-hide"); // lx 2.0
    const leadOverwriteCB = this.props.svconfig.registDispLeadOverWriteCheckbox && this.props.querySettings.registTarget !== 1 ?
          classnames("slds-grid slds-grid--vertical-stretch") : classnames("slds-hide");  // lx 2.0
    // lx 2.0 // 基本 v2.0.2 で、SB_NameCard_S1Config に enableLeadEdit を追加したので、
    const enableLeadEdit = typeof this.props.svconfig.enableLeadEdit !== "undefined" ? this.props.svconfig.enableLeadEdit : true;
    const styles = { padding: '12px' };
    return (
      <div>
        {/* ToDo: 未選択でOKしたときのPopupエラー */}
        <Popover key='11'
          ref={ ref => (this.popoverDoSelect = ref) }
          position='bottom-left'
          tooltip
          theme='error'
          style={{
            position: 'absolute',
            marginLeft: +4,
            marginTop: -30,
          }}>
          <div className="slds-text-body--regular">選択してください</div>
        </Popover>
        <div style={ styles } className="slds-scrollable">
          <RadioGroup label='' name='radiogroup1'>
          <ul className="slds-has-dividers--bottom-space">
            { opts }
          </ul>
          </RadioGroup>
          <Form>
            <div className={ overwriteSecsion }>
              <CheckboxGroup label='' >
                <div className={ acntOverwriteCB }>
                  <Checkbox label={`既存の${accountLabel}の情報を${namecardLabel}情報で上書きする`} value="2" checked={ this.props.acntParam.overwrite }
                    disabled={ !this.props.svconfig.enableAccountEdit }
                    onChange={ this.onFieldChange.bind(this, 'acntOverwrite') }/>
                </div>
                <div className={ leadOverwriteCB }>
                  <Checkbox label={`既存の${leadLabel}の情報を${namecardLabel}情報で上書きする`} value="2" checked={ this.props.leadParam.overwrite }
                    disabled={ !enableLeadEdit }
                    onChange={ this.onFieldChange.bind(this, 'leadOverwrite') }/>
                </div>
              </CheckboxGroup>
            </div>
          </Form>
          {/* フッターの分だけスペースをおく */}
          <div style={{ marginBottom: '48px' }}></div>
          {/* フッター */}
          <Footer center={{ label:"Cancel", icon: "ban", func: this.props.cancel }} right={{ label: "次へ", icon: "forward", func: this.goOn.bind(this) }} />
        </div>
      </div>
    );
  }

}
        // {//<ul className="slds-has-dividers--top">}


export default RegistAccount;



