// RegistFinishContainer.js
import React, { Component } from 'react';
import { connect } from 'react-redux';
import { changeStep, } from '../actions/index';
import RegistFinish from '../components/RegistFinish.js';

const mapStateToProps = (state, ownProps) => {
  const { step, loading, } = state.get('process').toObject();
  const svconfig = state.getIn(['config', 'svconfig']);
  const request = state.get('request').toObject();
  const res = { svconfig, request,
            location: ownProps.router.location, step, loading, };
  return res;
}

const mapDispatchToProps = (dispatch, ownProps) => {
  return {
    // 現在のステップを設定
    setCurrentStep: (step) => {
      dispatch(changeStep(step));
    },
    // レコードへ
    goto: (recId) => {
      if( (typeof sforce != 'undefined') && (sforce != null) ) {
        // S1, LEX
        sforce.one.navigateToURL('/' + recId);
      } else {
        // クラシック
        window.open('/' + recId, "_top");
      }
    },
    // レコード詳細へ
    cancel: (event) => {
      event.preventDefault();
      if( (typeof sforce != 'undefined') && (sforce != null) ) {
        // S1, LEX
        sforce.one.navigateToURL('/' + vfValue.targetId);
      } else {
        // クラシック
        window.open('/' + vfValue.targetId, "_top");
      }
    },
  }

}

export default connect(mapStateToProps, mapDispatchToProps)(RegistFinish);



