// RegistOptionContainer.js
import React, { Component } from 'react';
import { connect } from 'react-redux';
// import * as Immutable from 'immutable';
import {
  selectAccountLookuped, selectAccountRectype,
  selectContactLookuped, selectContactRectype,
  selectLeadLookuped, selectLeadRectype,  // lx 2.0
} from '../actions/index';
import RegistOption from '../components/RegistOption.js';

const mapStateToProps = (state, ownProps) => {
  const { step, loading, } = state.get('process').toObject();
  const registOptions = state.get('registOptions').toObject();
  const querySettings = state.getIn(['config', 'querySettings']);
  // const registers = state.getIn(['registOptions', 'registers']);
  const accountOptions = state.getIn(['registOptions', 'accountOptions']);
  let selectedOption;
  if (accountOptions) {
    accountOptions.forEach((item) => {
      if (item.selected) {
        selectedOption = item;
      }
    });
  }
  const registOption = ownProps.registOption;
  // const lookuped = state.getIn(['registOptions', (ownProps.sobjectname === 'Account' ? 'accountLookuped' : 'contactLookuped')]);
  // const recTypes = state.getIn(['config', 'recTypes']);
  // const recTypeId = recTypes && recTypes.length > 0 ? recTypes[0].id : null;
  const res = { registOption: ownProps.registOption, recTypes: ownProps.recTypes, accountId: ownProps.accountId, dispRectype: ownProps.dispRectype };
  return res;
}

const mapDispatchToProps = (dispatch, ownProps) => {
  return {
    //  ルックアップ選択
    selectLookuped: (lookuped) => {
      if (ownProps.sobjectname === 'Account') {
        dispatch(selectAccountLookuped(lookuped));
      }
      else if (ownProps.sobjectname === 'Contact') {
        dispatch(selectContactLookuped(lookuped));
      }
      else if (ownProps.sobjectname === 'Lead') {
        dispatch(selectLeadLookuped(lookuped));
      }
    },
    // レコードタイプ選択
    selectRecordType: (recType) => {
      if (ownProps.sobjectname === 'Account') {
        dispatch(selectAccountRectype(recType));
      }
      else if (ownProps.sobjectname === 'Contact') {
        dispatch(selectContactRectype(recType));
      }
      else if (ownProps.sobjectname === 'Lead') {
        dispatch(selectLeadRectype(recType));
      }
    },

    // 選択した名刺の登録候補一覧の表示
    fetchRegisters: (querySettings) => {
      dispatch(fetchRegisters({ cardIds: [vfValue.targetId], querySettings })).then((response) => {
        // let data = response.payload.data ? response.payload.data : 'Network Error';
        return !response.error ? dispatch(fetchRegistersSuccess(response.payload)) : dispatch(fetchRegistersFailure(response.payload));
      });
    },
    // 選択した名刺の登録先候補一覧の表示
    fetchAccountOptions: (querySettings) => {
      dispatch(fetchAccountOptions({ cardId: vfValue.targetId, querySettings })).then((response) => {
        // let data = response.payload.data ? response.payload.data : 'Network Error';
        return !response.error ? dispatch(fetchAccountOptionsSuccess(response.payload)) : dispatch(fetchAccountOptionsFailure({ ...response.payload, goback:true }));
      });
    },
    selectOption: (index) => {
      // let idx = event.target.value;
      dispatch(selectAccountOption(index));
    },
    // // 選択を変更
    // changeOption: (prevIndex, nextIndex) => {
    //   dispatch(changeAccountOption(prevIndex, nextIndex));
    // },
    // 戻る
    goBack: (event) => {
      event.preventDefault()
      ownProps.router.push({
        pathname: '/',
        // query: {
        //   qsparam: this.state.value
        // }
      })
    },
    // 次へ
    goOn: (event) => {
      event.preventDefault()
      ownProps.router.push({
        pathname: '/cont',
        // query: {
        //   qsparam: this.state.value
        // }
      });
    },
  }

}

export default connect(mapStateToProps, mapDispatchToProps)(RegistOption);



