import 'babel-polyfill';
import { createStore } from 'redux';
// import { combineReducers } from 'redux';
import { combineReducers } from 'redux-immutable';  // immutable !!
// import { reducer as formReducer } from 'redux-form';
import { reduxForm } from 'redux-form/immutable'
import config from './config-reducer';
import registOptions from './registOptions-reducer';
import request from './request-reducer';
import process from './process-reducer';

const rootReducer = combineReducers({
  config,         //<-- SvConfig
  registOptions,   // 名刺−登録先
  request,    // レコードタイプ 等の指定
  process,     // 操作ステータス
  // form: formReducer // <-- redux-form
  // form: reduxForm // <-- redux-form
});


export default rootReducer;



