// process-reducer.js
//  オペレーションのステータス
// @flow
// import * as Immutable from 'immutable';
import { Map } from 'immutable'
import {
  CHANGE_STEP, SHOW_MODALQUERYSETTINGS,
  // SHOW_MODALNOTIFICATION,
  ADD_ERROR, DEL_ERROR, SET_ALLOWRETRY, SET_ALLOWSAVE,

  FETCH_RECORDTYPES, FETCH_RECORDTYPES_SUCCESS, FETCH_RECORDTYPES_FAILURE,
  FETCH_SVCONFIG, FETCH_SVCONFIG_SUCCESS, FETCH_SVCONFIG_FAILURE,
  FETCH_USER, FETCH_USER_SUCCESS, FETCH_USER_FAILURE,

  FETCH_NAMECARD, FETCH_NAMECARD_SUCCESS, FETCH_NAMECARD_FAILURE,
  FETCH_REGISTERS, FETCH_REGISTERS_SUCCESS, FETCH_REGISTERS_FAILURE,
  FETCH_ACCOUNTOPTIONS, FETCH_ACCOUNTOPTIONS_SUCCESS, FETCH_ACCOUNTOPTIONS_FAILURE,
  FETCH_CONTACTOPTIONS, FETCH_CONTACTOPTIONS_SUCCESS, FETCH_CONTACTOPTIONS_FAILURE,
  FETCH_CAMPAGNOPTIONS, FETCH_CAMPAGNOPTIONS_SUCCESS, FETCH_CAMPAGNOPTIONS_FAILURE,

  FIND_ACCOUNT, FIND_ACCOUNT_SUCCESS, FIND_ACCOUNT_FAILURE,
  FIND_CONTACT, FIND_CONTACT_SUCCESS, FIND_CONTACT_FAILURE,
  // FIND_LEAD, FIND_LEAD_SUCCESS, FIND_LEAD_FAILURE,
  // FIND_CAMPAIGN, FIND_CAMPAIGN_SUCCESS, FIND_CAMPAIGN_FAILURE,

  DO_REGIST, DO_REGIST_SUCCESS, DO_REGIST_FAILURE,

} from '../actions/index';

const INITIAL_STATE = Map({
  step: 0,
  // selectQuery: 0,
  loading: true,
  errors: [],      // エラーが発生したら、その内容を配列の最後に加える。表示したのを閉じたらそのは消す。
  allowRetry: false, // 重複エラーで再試行可能なとき
  allowSave: false,  // 重複エラーでも保存するとしたとき
  modalQuerySettings: false,      // 検索設定モーダルの オン オフ
  // modalNotification: false,      // 通知モーダルの オン オフ
  accounts: [],
  contacts: [],
  // leads: [],
  // campaigns: [],
});

export default (state: Object = INITIAL_STATE, action: Object) => {
  // let errors = null;
  switch(action.type) {

  case CHANGE_STEP:
    return state.update("step", () => action.payload );

  case SHOW_MODALQUERYSETTINGS:
    return state.update("modalQuerySettings", () => action.payload );

  // case SHOW_MODALNOTIFICATION:
  //   return state.update("modalNotification", () => action.payload );

  case ADD_ERROR:
    return state.update("errors", (item) => {
      item.push({ content: action.payload, notify: true });
      item = item.filter((e, idx) => e.notify );      // 表示済み false  のをけして、表示するのを先頭に
      return item;
    });

  case DEL_ERROR:
    return state.update("errors", (item) => {
      item = item.map((e, idx) => {
        if (idx === action.payload) {
          e.notify = false;
        }
        return e;
      });

      return item;
    });

  // 重複エラーで再試行可能なとき
  case SET_ALLOWRETRY:
    return state.merge(Map({ allowRetry: action.payload }));

  // 重複エラーでも保存するとしたとき
  case SET_ALLOWSAVE:
    return state.merge(Map({ allowSave: action.payload }));

  // SFDC と通信開始
  case FETCH_SVCONFIG:
  case FETCH_USER:
  case FETCH_RECORDTYPES:
  case FETCH_NAMECARD:
  case FETCH_REGISTERS:
  case FETCH_ACCOUNTOPTIONS:
  case FETCH_CONTACTOPTIONS:
  case FETCH_CAMPAGNOPTIONS:
  case DO_REGIST:
    return state.merge(Map({ loading: true }));

  // SFDC と通信完了 成功
  case FETCH_SVCONFIG_SUCCESS:
  case FETCH_USER_SUCCESS:
  case FETCH_RECORDTYPES_SUCCESS:
  case FETCH_NAMECARD_SUCCESS:
  case FETCH_REGISTERS_SUCCESS:
  case FETCH_ACCOUNTOPTIONS_SUCCESS:
  case FETCH_CONTACTOPTIONS_SUCCESS:
  case FETCH_CAMPAGNOPTIONS_SUCCESS:
  case DO_REGIST_SUCCESS:
    return state.merge(Map({ loading: false }));

  // SFDC と通信完了 エラー
  case FETCH_SVCONFIG_FAILURE:
  case FETCH_USER_FAILURE:
  case FETCH_RECORDTYPES_FAILURE:
  case FETCH_NAMECARD_FAILURE:
  case FETCH_REGISTERS_FAILURE:
  case FETCH_ACCOUNTOPTIONS_FAILURE:
  case FETCH_CONTACTOPTIONS_FAILURE:
  case FETCH_CAMPAGNOPTIONS_FAILURE:
  case DO_REGIST_FAILURE:
    let errors = state.get("errors");
    errors.push({ content: action.payload, notify: true });
    errors = errors.filter((e, idx) => e.notify );
    return state.merge(Map({ loading: false, errors }));
    // return state.update((items) => {
    //   for (let [key, item] of items) {
    //     if (key === "errors") {
    //       item.push({ content: action.payload, notify: true });
    //       item = item.filter((e, idx) => e.notify );      // 表示済み false  のをけして、表示するのを先頭に
    //     }
    //     else if (key === "loading") {
    //       item = false;
    //     }
    //     items.set(key, item);
    //   };
    //   return items;
    // });

  // アカウントの検索 結果を保持しとく
  case FIND_ACCOUNT:
    return state.merge(Map({ accounts: [], loading: true, error: null }));
  case FIND_ACCOUNT_SUCCESS:
    return state.merge(Map({ accounts: action.payload, loading: false, error: null }));
  case FIND_ACCOUNT_FAILURE:
    errors = state.get('errors');
    errors.push({ content: action.payload, notify: true });
    return state.merge(Map({ accounts: [], loading: false, errors }));

  // コンタクトの検索 結果を保持しとく
  case FIND_CONTACT:
    return state.merge(Map({ contacts: [], loading: true, error: null }));
  case FIND_CONTACT_SUCCESS:
    return state.merge(Map({ contacts: action.payload, loading: false, error: null }));
  case FIND_CONTACT_FAILURE:
    errors = state.get('errors');
    errors.push({ content: action.payload, notify: true });
    return state.merge(Map({ accounts: [], loading: false, errors }));

  // // リードの検索 結果を保持しとく lx 2.0
  // case FIND_LEAD:
  //   return state.merge(Map({ leads: [], loading: true, error: null }));
  // case FIND_LEAD_SUCCESS:
  //   return state.merge(Map({ leads: action.payload, loading: false, error: null }));
  // case FIND_LEAD_FAILURE:
  //   errors = state.get('errors');
  //   errors.push({ content: action.payload, notify: true });
  //   return state.merge(Map({ leads: [], loading: false, errors }));


  default:
    return state;
  }
}



