/**
 *
 * RegistOption.js
 * 個別登録の 登録先候補の選択肢
 *  lx2.0 18.09.xx SV_DEV-1155 【例外】レコードタイプを１つのみ設定している場合に登録画面で表示が異なる。→ １つなら Select をださない。
 **/
//
//
import React, { Component, PropTypes } from 'react';
import { Link } from 'react-router';
import LookupTarget from './LookupTarget'
import { Radio, Checkbox, Icon, Popover, Form, Select, Option, Lookup } from '../rlds';//'react-lightning-design-system';

class RegistOption extends Component {
  onLookupChange(value) {
    // this.setState({ ['lookup']: value });     //
    this.props.selectLookuped(value);
  }

  onFieldChange(name, e, value) {
    // this.setState({ [name]: value });     //
    if (name === "recTypeId") {
      let param = { id: value, name: e.target.options[e.target.selectedIndex].innerHTML };
      this.props.selectRecordType(param);
    }
  }

  // {
  //   "selected" : false,
  //   "id" : "001100000182989AAA",
  //   "index" : 0,
  //   "title" : "コネクシオ本舗",
  //   "type" : 1,
  //   "label2val" : {
  //     "取引先 部門": "法人ビジネス企画部 コンサルティングサービス課",
  //     "市区郡(請求先)": "新宿区",
  //     "町名・番地(請求先)": "西新宿8-17-1住友不動産新宿グランドタワー37F",
  //     "郵便番号(請求先)": "〒160-6137",
  //     "都道府(請求先)": "東京都"},
  //   "contactCount" : 0,
  //   "oppotunityCount" : 1,
  //   "summary": ["東京都", "法人ビジネス企画部 コンサルティングサービス課"],
  //   "values" : ["〒160-6137", "東京都", "新宿区", "西新宿8-17-1住友不動産新宿グランドタワー37F", "法人ビジネス企画部 コンサルティングサービス課"]
  // },
  // {
  //   "selected" : false,
  //   "index" : 2,
  //   "info" : "既存の取引先を検索して指定します。",
  //   "title" : "その他の取引先を指定",
  //   "type" : 2
  // },
  // {
  //   "selected" : false,
  //   "index" : 2,
  //   "info" : "取引先を新たに追加します。",
  //   "title" : "新規取引先追加",
  //   "type" : 3
  // }

  makeContent(values) {
    if (!values)  return null;
    let lines =
    values.map((val, idx) => {
      return <li key={ idx }>{ val }</li>;
    });
    let result =  <ul> { lines } </ul>;
    return result;
  }

  clickHandler(name, e, value) {
    // let recType = React.findDOMNode(this.refs.recType);
    console.log(this.state);
    this.props.selectHandler(name, e, value);
  }

  render() {

    let { index, title, selected, id, otype, type, label2val, contactCount, oppotunityCount, summary, values, info, recTypeId, recTypeName, lookuped } = this.props.registOption;
    let content = values ? this.makeContent(values) : (info ? info : "");

    let selectRecType; // Select tag record type
    // 新規作成でレコードタイプを選ぶ用 v1.20 レコードタイプ選択表示設定反映
    if (type === 3 && this.props.dispRectype && this.props.recTypes && this.props.recTypes.infos && this.props.recTypes.infos.length > 1) {
      let defRectype = {
        id: recTypeId ? recTypeId : this.props.recTypes.infos[0].id,
        name: recTypeName ? recTypeName : this.props.recTypes.infos[0].label,
      };
      if (!recTypeId) {
        // レコードタイプID初期値がなかったとき
        this.props.selectRecordType(defRectype);
      }
      let opts = this.props.recTypes.infos.map((info, n) => <Option key={ n } value={ info.id }>{ info.label }</Option>);
      selectRecType =
        <Form type='inline'>
          <Select ref="recType" className="slds-text-body--regular"
            label="レコードタイプ"
            onChange={ this.onFieldChange.bind(this, 'recTypeId') }
            defaultValue={ defRectype.id } >
          { opts }
          </Select>
        </Form>;
    }

    // 候補以外の既存レコードを選ぶ用
    let lookup;
    if (type === 2) {
      let accountId = this.props.accountId ? this.props.accountId : null;
      lookup = <LookupTarget ref="lookup" sobject={ otype } onSelectHandler={ this.onLookupChange.bind(this) }
            registOption={ this.props.registOption } accountId={ accountId } recTypeIds={ this.props.recTypeIds } />;
    }

    let iconname = otype.toLowerCase();
    return(
      <div className="slds-tile slds-media" >
        <div className="slds-media__figure" onClick={ this.clickHandler.bind(this, index) } >
          <div className="slds-grid slds-grid--vertical-align-center slds-grid--align-center">
            <Radio label='' value={ index } checked={ this.props.registOption.selected }/>
          </div>
        </div>
        <div className="slds-media__figure" onClick={ this.clickHandler.bind(this, index) } >
          <Icon category='standard' icon={ iconname } className='slds-m-right--small'/>
        </div>
        <div className="slds-media__body" >
          <div className="slds-grid slds-nowrap" >
            <div className="slds-col slds-size--1-of-2 slds-m-horizontal--x-small" onClick={ this.clickHandler.bind(this, index) } >
              <Popover key={ 'p'+index }
                ref={ ref => (this.popover = ref) }
                position='top-left'
                tooltip
                theme='info'
                style={{
                  position: 'absolute',
                  marginTop: +24,
                }}>
                <div className="slds-text-body--regular">{ content }</div>
              </Popover>
              <h3 className="slds-truncate slds-section__title" title="">
                  <a href="javascript:void(0);" onMouseEnter={ () => this.popover.toggle(true) }
                                  onMouseLeave={ () => this.popover.toggle(false) } >{ title }</a>
              </h3>
              <div className="slds-tile__detail slds-text-body--small">
                <ul className="slds-list--horizontal slds-has-dividers--right">
                  <li className="slds-item">{ summary ? summary.toString() : "" }</li>
                </ul>
              </div>

            </div>
            <div className="slds-col slds-size--1-of-2 slds-m-horizontal--x-small">
              { type === 2 ? lookup : null }
              { type === 3 ? selectRecType : null }
            </div>
          </div>

        </div>
      </div>
    );
  }
}

RegistOption.propTypes = {
  selectLookuped: PropTypes.func,
  selectRecordType: PropTypes.func,
  selectHandler: PropTypes.func,
  registOption: PropTypes.object,
  recTypes: PropTypes.object,
  accountId: PropTypes.string,
  sobjectname: PropTypes.string,
};

export default RegistOption;



