// RegistAccountContainer.js
import React, { Component } from 'react';
import { connect } from 'react-redux';
// import * as Immutable from 'immutable';
import {
  changeStep,
  fetchRegisters, fetchRegistersSuccess, fetchRegistersFailure,
  fetchAccountOptions, fetchAccountOptionsSuccess, fetchAccountOptionsFailure,
  selectAccountOption,
  setRegistTarget, setAccountParam,
  setLeadParam, // lx 2.0
  setCampaignParam, // lx 2.0
  setOverwrite, } from '../actions/index';
import RegistAccount from '../components/RegistAccount.js';

const mapStateToProps = (state, ownProps) => {
  const { step, loading, ...process } = state.get('process').toObject();
  const svconfig = state.getIn(['config', 'svconfig']);
  const registOptions = state.get('registOptions').toObject();
  const querySettings = state.getIn(['config', 'querySettings']);
  const accountOptions = state.getIn(['registOptions', 'accountOptions']);
  const recTypes = state.getIn(['config', 'recTypes']);
  const acntParam = state.getIn(['request', 'acntParam']);
  const leadParam = state.getIn(['request', 'leadParam']);
  const res = { svconfig, acntParam, leadParam, registOptions: accountOptions, recTypes : recTypes, querySettings,
            location: ownProps.router.location, step, loading };
  return res;
}

const mapDispatchToProps = (dispatch, ownProps) => {
  console.log(ownProps);
  return {
    // 現在のステップを設定
    setCurrentStep: (step) => {
      dispatch(changeStep(step));
    },
    // 選択した名刺の登録先候補一覧の表示
    fetchAccountOptions: (querySettings) => {
      dispatch(fetchAccountOptions({ cardId: vfValue.targetId, querySettings })).then((response) => {
        return !response.error ? dispatch(fetchAccountOptionsSuccess(response.payload)) : dispatch(fetchAccountOptionsFailure(response.payload));
      });
    },
    // 候補を選択
    selectOption: (index) => {
      dispatch(selectAccountOption(index));
    },
    //  上書きする の設定更新
    toggleOverwrite: (sobjectname, val) => {
      dispatch(setOverwrite({sobjectname, val}));
    },
    // レコード詳細へ
    cancel: (event) => {
      event.preventDefault();
      if( (typeof sforce != 'undefined') && (sforce != null) ) {
        // S1, LEX
        sforce.one.navigateToURL('/' + vfValue.targetId);
      } else {
        // クラシック
        window.open('/' + vfValue.targetId, "_top");
      }
    },
    // 次へ
    goOn: (opt) => {
      let targetId = opt.type === 1 ? opt.id : (opt.type === 2 ? opt.lookuped.value : null);
      // 選択した対象をキープ
      if (opt.otype === "Account") {
        // アカウントを選択
        dispatch(setRegistTarget({
          pType: "1",
          acntId: targetId,
          acntName: opt.type === 1 ? opt.title : (opt.type === 2 ? opt.lookuped.label : null),
        }));
        //  レコードタイプと、上書き設定をキープ
        dispatch(setAccountParam({
          recTypeId : opt.type === 3 && opt.recTypeId ? opt.recTypeId : null,
          recTypeName : opt.type === 3 && opt.recTypeName ? opt.recTypeName : null,
          allowSave : false,
        }));
        let pathname = "/cont";   // コンタクト 選択へ
        ownProps.router.push({
          pathname,
          query: {
            proc: "acnt",
            ope: opt.type === 3 ? "create" : "update",
            acntId: targetId,
            prev: "/",
          }
        });
      }
      else {
        // リードを選択
        dispatch(setRegistTarget({
          pType: "2",
          leadId: targetId,
          leadName: opt.type === 1 ? opt.title : (opt.type === 2 ? opt.lookuped.label : null),
        }));
        if (opt.type !== 3) {
          dispatch(setCampaignParam({
            campaignId: null,
            campaignName: null,
            statusObj: null,
          }));
        }
        //  レコードタイプと、上書き設定をキープ
        dispatch(setLeadParam({
          recTypeId : opt.type === 3 && opt.recTypeId ? opt.recTypeId : null,
          recTypeName : opt.type === 3 && opt.recTypeName ? opt.recTypeName : null,
          allowSave : false,
        }));
        let pathname = opt.type === 3 ? "/camp" : "/confirm"; // キャンペーン選択へ
        ownProps.router.push({
          pathname,
          query: {
            proc: "lead",
            ope: opt.type === 3 ? "create" : "update",
            leadId: targetId,
            prev: "/",
          }
        });
      }
    },
  }

}

export default connect(mapStateToProps, mapDispatchToProps)(RegistAccount);



