// RegistCampaignContainerjs
import React, { Component } from 'react';
import { connect } from 'react-redux';
import { changeStep,
      fetchCampaignOptions, fetchCampaignOptionsSuccess, fetchCampaignOptionsFailure,
      fetchCampaignStatusList, fetchCampaignStatusListSuccess, fetchCampaignStatusListFailure,
      fetchCampaignMemberStatusList, fetchCampaignMemberStatusListSuccess, fetchCampaignMemberStatusListFailure,
      selectCampaignOption,
      selectCampaignLookuped,
      selectCampaignMemberStatus,
      setCampaignParam,  } from '../actions/index';
import RegistCampaign from '../components/RegistCampaign.js';

const mapStateToProps = (state, ownProps) => {
  const { step, loading, ...process } = state.get('process').toObject();
  const svconfig = state.getIn(['config', 'svconfig']);
  const request = state.get('request').toObject();
  // const contactOptions = state.getIn(['registOptions', 'contactOptions']);
  const registOptions = state.get('registOptions').toObject();
  // const campaignOptions = state.getIn(['registOptions', 'campaignOptions']);  // lx 2.0
  // const campaignStatuss = state.getIn(['registOptions', 'campaignStatuss']);  // lx 2.0
  // const campaignMemberStatuss = state.getIn(['registOptions', 'campaignMemberStatuss']);  // lx 2.0
  // const selectedCampaignMemberStatus = state.getIn(['registOptions', 'selectedCampaignMemberStatus']);  // lx 2.0
  // const recTypes = state.getIn(['config', 'recTypes']);
  const card = state.getIn(['registOptions', 'card']);
  const querySettings = state.getIn(['config', 'querySettings']);
  const res = { svconfig,
            sobjectname: 'Campaign',
            // cardId: card.id, recTypes : recTypes.Contact,
            // registParam: request.contParam,
            // accountId: request.target.acntId,
            campaignParam: request.campaignParam,
            // registOptions: contactOptions,
            campaignOptions: registOptions.campaignOptions,
            campaignStatuss: registOptions.campaignStatuss,
            campaignMemberStatuss: registOptions.campaignMemberStatuss,
            selectedCampaignMemberStatus: registOptions.selectedCampaignMemberStatus,
            querySettings,
            location: ownProps.router.location, step, loading, };
  return res;
}

const mapDispatchToProps = (dispatch, ownProps) => {
  return {
    // 現在のステップを設定
    setCurrentStep: (step) => {
      dispatch(changeStep(step));
    },
    // 有効なキャンペーンのリスト取得
    fetchCampaignOptions: () => {
      dispatch(fetchCampaignOptions()).then((response) => {
        // let data = response.payload.data ? response.payload.data : 'Network Error';
        return !response.error ? dispatch(fetchCampaignOptionsSuccess(response.payload)) : dispatch(fetchCampaignOptionsFailure(response.payload));
      });
    },
    // 有効なキャンペーンのリスト取得
    fetchCampaignStatusList: () => {
      dispatch(fetchCampaignStatusList()).then((response) => {
        // let data = response.payload.data ? response.payload.data : 'Network Error';
        return !response.error ? dispatch(fetchCampaignStatusListSuccess(response.payload)) : dispatch(fetchCampaignStatusListFailure(response.payload));
      });
    },

    //  キャンペーン ルックアップ選択
    selectLookuped: (lookuped) => {
      dispatch(selectCampaignLookuped(lookuped));
      // if (lookuped && selected) {
      //   //選択してたら
      //   dispatch(fetchCampaignMemberStatusList({ campaignId: lookuped.value })).then((response) => {
      //     return !response.error ? dispatch(fetchCampaignMemberStatusListSuccess(response.payload)) : dispatch(fetchCampaignMemberStatusListFailure(response.payload));
      //   });
      // }
    },

    // キャンペーンを選択
    selectCampaignOption: (index, campaignOptions) => {
      // let idx = event.target.value;
      dispatch(selectCampaignOption(index));
      // let campaignId;
      // if (campaignOptions[index].type === 4 || (campaignOptions[index].type === 2 && !campaignOptions[index].lookuped)) {
      //   //  キャンペーンメンバーに追加しない。 または キャンペーン選択で未選択 なら ステータス選択肢をクリア
      //   campaignId = null;
      // }
      // else {
      //   campaignId = campaignOptions[index].id;
      // }
      // if (campaignId === null)  {
      //   //  キャンペーンメンバーに追加しない。 なら ステータス選択肢をクリア
      //   dispatch(fetchCampaignMemberStatusListSuccess([]));
      // }
      // else {
      //   // dispatch(fetchCampaignMemberStatusList({ campaignId: campaignOptions[index].id })).then((response) => {
      //   dispatch(fetchCampaignMemberStatusList({ campaignId })).then((response) => {
      //     if (!response.error) {
      //       dispatch(fetchCampaignMemberStatusListSuccess(response.payload));
      //       let items = response.payload;
      //       let defaultStatus;
      //       items.map((item, n) => {
      //         if (n === 0 || item.IsDefault) {
      //           defaultStatus = item;
      //         }
      //       });
      //       dispatch(selectCampaignMemberStatus(defaultStatus));
      //     }
      //     else {
      //       dispatch(fetchCampaignMemberStatusListFailure(response.payload));
      //     }
      //   });
      // }

    },

    setCampaignMemberStatusS: (campaignId) => {
      if (campaignId === null)  {
        //  キャンペーンメンバーに追加しない。 なら ステータス選択肢をクリア
        dispatch(fetchCampaignMemberStatusListSuccess([]));
        dispatch(selectCampaignMemberStatus(null));
      }
      else {
        // dispatch(fetchCampaignMemberStatusList({ campaignId: campaignOptions[index].id })).then((response) => {
        dispatch(fetchCampaignMemberStatusList({ campaignId })).then((response) => {
          if (!response.error) {
            dispatch(fetchCampaignMemberStatusListSuccess(response.payload));
            let items = response.payload;
            let defaultStatus;
            items.map((item, n) => {
              if (n === 0 || item.IsDefault) {
                defaultStatus = item;
              }
            });
            dispatch(selectCampaignMemberStatus(defaultStatus));
          }
          else {
            dispatch(fetchCampaignMemberStatusListFailure(response.payload));
          }
        });
      }
    },

    // キャンペーンメンバーの状況 を選択
    selectCampaignMemberStatus: (status) => {
      dispatch(selectCampaignMemberStatus(status));
    },
    // 戻る
    goBack: (event) => {
      event.preventDefault();
      ownProps.router.push({
        pathname: '/',
        query: {
          pType: "2",
          // lid: ownProps.location.query.lid,
          prev: '/camp',
        },
      });
    },
    // レコード詳細へ
    cancel: (event) => {
      event.preventDefault();
      if( (typeof sforce != 'undefined') && (sforce != null) ) {
        // S1, LEX
        sforce.one.navigateToURL('/' + vfValue.targetId);
      } else {
        // クラシック
        window.open('/' + vfValue.targetId, "_top");
      }
    },
    // 次へ
    goOn: (opt) => {
      let campId = opt.type === 1 ? opt.id : (opt.type === 2 ? opt.lookuped.value : null);
      //  レコードタイプと、上書き設定をキープ
      dispatch(setCampaignParam({
        campaignId: campId,
        campaignName: opt.type === 1 ? opt.title : (opt.type === 2 ? opt.lookuped.label : null),
        statusObj: opt.status,
      }));
      let query = Object.assign({}, ownProps.router.location.query, { campId, prev: "/camp" });
      if (opt.status) {
        query["status"] = opt.status.Label;
      }
      ownProps.router.push({
        pathname: '/confirm',
        query
      });
    },

  }

}

export default connect(mapStateToProps, mapDispatchToProps)(RegistCampaign);



