// RegistFinish.js
// 名刺と登録先のオプションのリスト
import React, { Component } from 'react';
import { Link } from 'react-router';
import Footer from './Footer';
import {
  Button, Form, Input, Textarea, RadioGroup, Radio,
  CheckboxGroup, Checkbox, Select, Option,
  Picklist, PicklistItem,
  DateInput,
  Lookup,
  FieldSet,
  Icon,
} from '../rlds';
import { getRecordById } from '../api/sfdc';

function fetchRecordById(id, callback) {
  getRecordById(id).then((response) => {
    if (!response.error) {
      callback(response);
    }
    else {
      console.log('getRecordById: ' + response);
    }
  });
}


class RegistFinish extends Component {
  constructor(props) {
    super(props);
    console.log("location:");
    console.log(this.props.location);
    this.state = { acnt: null, cont: null, lead: null };
  }
// cardId:"a077F000005Mc3lQAC"
// lead:{id: "00Q7F00000E2zfRUAR", name: "網本 伸大", ownerId: "0057F000000JEjWQAW", ownerName: "沢野 Lead拡張開発01"}
// leadCampaignMember:{id: "00v7F000004em2lQAA", name: "網本 伸大"}
// todo:{id: "00T7F000012zF2VUAU", name: "名刺交換", ownerId: "0057F000000JEjWQAW"}

  componentDidMount() {
    // プログレスバーの設定
    this.props.setCurrentStep(2);
    // 登録した アカウントとコンタクト
    if (this.props.request && this.props.request.response && this.props.request.response.registeds) {
      const m =  this.props.request.response.registeds;
      let result = m[this.props.request.target.cardId];
      if (typeof result.lead === 'undefined') {
        this.setState({ acnt: result.acnt, cont: result.cont });
      }
      else {
        this.setState({ lead: result.lead, campaignMember: result.leadCampaignMember });
      }
    }
    else {
      //
      if (this.props.location.query.acntId) {
        fetchRecordById(this.props.location.query.acntId, (res)=> { this.setState({ acnt: res }); console.log(this.state);});
      }
      if (this.props.location.query.contId) {
        fetchRecordById(this.props.location.query.contId, (res)=> { this.setState({ cont: res }); console.log(this.state);});
      }
      if (this.props.location.query.leadId) {
        fetchRecordById(this.props.location.query.leadId, (res)=> { this.setState({ lead: res }); console.log(this.state);});
      }
    }
  }

  gotoRecord(recId, event) {
    event.preventDefault();
    this.props.goto(recId);
  }

  makeAccountRegistedInfo() {
    const { accountLabel, contactLabel  } = this.props.svconfig;
    let result =
      <div>
        <div className="slds-list--horizontal slds-wrap slds-m-top--x-small">
          <div className="slds-item--label slds-text-color--weak slds-truncate" title={ accountLabel }>
            <Icon category="standard" icon='account' className="slds-icon--x-small"/>&nbsp;{ accountLabel }:
          </div>
          <div className="slds-item--detail slds-truncate" style={{ fontWeight: "bolder" }}>
            {/*<a onClick={ this.gotoRecord.bind(this, result.acnt.id) } >
              {result.acnt.name}
            </a>*/}
            <a onClick={ this.gotoRecord.bind(this, this.state.acnt.id) } >
              {this.state.acnt.name}
            </a>
          </div>
        </div>
        <div className="slds-list--horizontal slds-wrap slds-m-top--small">
          <div className="slds-item--label slds-text-color--weak slds-truncate" title={ contactLabel }>
            <Icon category="standard" icon='contact' className="slds-icon--x-small"/>&nbsp;{ contactLabel }:
          </div>
          <div className="slds-item--detail slds-truncate" style={{ fontWeight: "bolder" }}>
            {/*<a  onClick={ this.gotoRecord.bind(this, result.cont.id) } class="slds-text-link--reset">
              {result.cont.name}
            </a>*/}
            <a  onClick={ this.gotoRecord.bind(this, this.state.cont.id) } class="slds-text-link--reset">
              {this.state.cont.name}
            </a>
          </div>
        </div>
      </div>;
    return result;
  }

  makeLeadRegistedInfo() {
    const leadLabel = this.props.svconfig.labels.lead.objName;
    const campaignMemberLabel = this.props.svconfig.labels.campaignMember ? this.props.svconfig.labels.campaignMember.objName : 'キャンペーンメンバー';
    const statusLabel = this.props.svconfig.labels.campaignMember ? this.props.svconfig.labels.campaignMember.statusLabel : 'キャンペーンメンバーの状況';

    let campaignMember; // キャンペーンメンバー の情報
    if (this.state.campaignMember) {
      campaignMember =
        <div className="slds-list--horizontal slds-wrap slds-m-top--small">
          <div className="slds-item--label slds-text-color--weak slds-truncate" title={ campaignMemberLabel }>
            <Icon category="standard" icon='contact' className="slds-icon--x-small"/>&nbsp;{ campaignMemberLabel }:
          </div>
          <div className="slds-item--detail slds-truncate" style={{ fontWeight: "bolder" }}>
            {/*<a  onClick={ this.gotoRecord.bind(this, result.cont.id) } class="slds-text-link--reset">
              {result.cont.name}
            </a>*/}
            <a  onClick={ this.gotoRecord.bind(this, this.state.campaignMember.id) } class="slds-text-link--reset">
              {this.state.campaignMember.name}
            </a>
          </div>
        </div>;
    }

    let result =
      <div>
        <div className="slds-list--horizontal slds-wrap slds-m-top--x-small">
          <div className="slds-item--label slds-text-color--weak slds-truncate" title={ leadLabel }>
            <Icon category="standard" icon='account' className="slds-icon--x-small"/>&nbsp;{ leadLabel }:
          </div>
          <div className="slds-item--detail slds-truncate" style={{ fontWeight: "bolder" }}>
            {/*<a onClick={ this.gotoRecord.bind(this, result.acnt.id) } >
              {result.acnt.name}
            </a>*/}
            <a onClick={ this.gotoRecord.bind(this, this.state.lead.id) } >
              { this.state.lead.name }
            </a>
          </div>
        </div>
        { campaignMember }
      </div>;
    return result;
  }

  render() {
    console.log('render: ');
    console.log(this.state);
    if (this.props.loading || !this.props.request || !this.props.request.response || (!this.state.acnt && !this.state.lead)) {
      return <div></div>;
    }
    const { createTodo, subject, activityDate, comment, } = this.props.request.todoParam;
    const styles = { padding: '20px' };
    const m =  this.props.request.response.registeds;
    let result = m[this.props.request.target.cardId];

    let registInfo;
    if (this.props.request.target.pType !== "2") {
      // アカウント
      registInfo = this.makeAccountRegistedInfo();
    }
    else {
      // リード
      registInfo = this.makeLeadRegistedInfo();
    }

    return (
      <div style={ styles }>
        {/* 登録結果情報 */}
        <div className="slds-box slds-theme--shade">
          <h3 className="slds-section-title">以下に登録しました。</h3>
          { registInfo }
        </div>
        <div className="slds-m-vertical--small"/>
        {/* フッターの分だけスペースをおく */}
        <div style={{ marginBottom: '48px' }}></div>
        {/* フッター */}
        <Footer center={{ label:"OK", icon: "ban", func: this.props.cancel }}/>
      </div>
    );
  }


}

export default RegistFinish;



