'use strict';

import React from 'react';
import classnames from 'classnames';
import Helpers from './mixins/helpers';

export var PrevArrow = React.createClass({

  clickHandler: function (options, e) {
    if (e) { e.preventDefault(); }
    this.props.clickHandler(options, e);
  },
  render: function () {
    var prevClasses = {'slick-arrow': true, 'slick-prev': true};
    var prevHandler = this.clickHandler.bind(this, {message: 'previous'});

    if (!this.props.infinite && (this.props.currentSlide === 0 || this.props.slideCount <= this.props.slidesToShow)) {
      prevClasses['slick-disabled'] = true;
      prevHandler = null;
    }

    var prevArrowProps = {
      key: '0',
      'data-role': 'none',
      className: classnames(prevClasses),
      style: {display: 'block'},
      onClick: prevHandler
    };
    var prevArrow;

    if (this.props.prevArrow) {
      prevArrow = React.cloneElement(this.props.prevArrow, prevArrowProps);
    } else {
      // SV_DEV-704 Lighting Design System は 静的リソースは使わず<apex:slds /> で引用する。
      // 2018.01.xx v1.22 <apex:slds /> をつかうと 矢印の下に Previous の文字が表示される
      // prevArrow = <button key='0' type='button' {...prevArrowProps}> Previous</button>;
      prevArrow = <button key='0' type='button' {...prevArrowProps}></button>;
    }

    return prevArrow;
  }
});


export var NextArrow = React.createClass({
  clickHandler: function (options, e) {
    if (e) { e.preventDefault(); }
    this.props.clickHandler(options, e);
  },
  render: function () {
    var nextClasses = {'slick-arrow': true, 'slick-next': true};
    var nextHandler = this.clickHandler.bind(this, {message: 'next'});

    if (!Helpers.canGoNext(this.props)) {
      nextClasses['slick-disabled'] = true;
      nextHandler = null;
    }

    var nextArrowProps = {
      key: '1',
      'data-role': 'none',
      className: classnames(nextClasses),
      style: {display: 'block'},
      onClick: nextHandler
    };

    var nextArrow;

    if (this.props.nextArrow) {
      nextArrow = React.cloneElement(this.props.nextArrow, nextArrowProps);
    } else {
      // SV_DEV-704 Lighting Design System は 静的リソースは使わず<apex:slds /> で引用する。
      // 2018.01.xx v1.22 <apex:slds /> をつかうと 矢印の下に Next の文字が表示される
      // nextArrow = <button key='1' type='button' {...nextArrowProps}> Next</button>;
      nextArrow = <button key='1' type='button' {...nextArrowProps}></button>;
    }

    return nextArrow;
  }
});



