// RegistConfirm.js
// 名刺と登録先のオプションのリスト
/*
RLDS の DatePicker  の修正
render の最初の方に以下を入れて日本語化する
moment.lang('ja', {
    weekdays: ["日曜日","月曜日","火曜日","水曜日","木曜日","金曜日","土曜日"],
    weekdaysShort: ["日","月","火","水","木","金","土"],   //
    weekdaysMin: ["日","月","火","水","木","金","土"],
    monthsShort:["1月", "2月", "3月", "4月", "5月", "6月", "7月", "8月", "9月", "10月", "11月", "12月"],
});
  ↑ いらなかった。
  ここの  render  で以下を入れとけばOK
  moment.locale('ja');

Footerより前に出るようにする。
style={{ zIndex: 9001 }}

*/
import React, { Component } from 'react';
import { Link } from 'react-router';
import classnames from 'classnames';
import moment from 'moment';
import Footer from './Footer';
import RadioGroup2 from './RadioGroup2';
import LookupSObject from './LookupSObject';
import {
  Button, Form, Input, Textarea, Row, Col, Radio,
  CheckboxGroup, Checkbox, Select, Option,
  Picklist, PicklistItem,
  DateInput,
  Lookup,
  FieldSet,
  Icon, Popover,
} from '../rlds';
// } from 'react-lightning-design-system';


const TodayButtonExtensionRenderer = (props) => {
  const { onSelect } = props; // eslint-disable-line react/prop-types
  const today = moment().format('YYYY-MM-DD');
  return (
    <div style={{ padding: '4px', textAlign: 'center' }}>
      <Button
        className='slds-size--1-of-2'
        onClick={ () => onSelect(today) }
      >
        今日
      </Button>
    </div>
  );
};

class RegistConfirm extends Component {
  constructor(props) {
    super(props);
    this.state = {
      createTodo: this.props.request.todoParam.createTodo,
      subject : this.props.request.todoParam.subject,
      activityDate : this.props.request.todoParam.activityDate,
      comment : this.props.request.todoParam.comment,
      ownerChoice: 0,
      lookupedOwner: this.props.request.owner.choice === 2 ? this.props.request.owner.user : null,
      allowSave: false,
      ownerObject: "user",
    };
  }

  componentWillMount() {
    // 所有者 初期値は実行ユーザを所有者
    this.props.selectRecordOwner(0, this.props.svconfig.user);    // 実行ユーザを所有者
  }

  componentDidMount() {
    this.props.setCurrentStep(2);
  }

  componentWillUnmount() {
    if (this.props.request.response!==null && this.props.request.response.success === false) {
      //  エラーが会った場合に前画面にもどるようなときに、レスポンスの内容はクリアする。
      this.props.clearResponse();
    }
  }

  onLookupChange(value) {
    this.setState({ ownerChoice: 2, lookupedOwner: value });
    this.props.selectRecordOwner(2, value);
  }

  onFieldChange(name, e, value) {
    let v;
    switch(name) {
    case 'createTodo':
    case 'allowSave':
      v = e.target.checked;
      break;
    case 'comment':
    case 'ownerChoice':
      v = e.target.value;
      break;
    case 'activityDate':
      v = new Date(value);
      break;
    default:
      v = value;
    }
    this.setState({ [name]: v });
    if (name !== 'ownerChoice') {
      this.props.changeTodoParam({ [name]: v });
    }
    else {
      let user;
      v  = Number(v);     // ToDo 数値でとれない！ なぜ！
      this.setState({ [name]: v });
      switch(v) {
      case 1:
        user = { id: this.props.registOptions.card.ownerId, name: this.props.registOptions.card.ownerName }; // カードの所有者
        break
      case 2:
        user = this.state.lookupedOwner;
        break;
      default:
        user = this.props.svconfig.user;    // 自分
      }
      this.props.selectRecordOwner(v, user);
    }
  }

  goOn(event) {
    event.target.disabled = "disabled";     // v1.20.1 2017.08.04. 保存ボタンを無効化する。
    event.preventDefault();
    // [this.props.selectedIndex];
    this.props.goOn(this.props.request, this.state.allowSave);
  }

  gotoRecord(recId, event) {
    event.preventDefault();
    this.props.goto(recId);
  }

  makeApexResultInfo(result) {
    let lines = []
    let bulkMode = result.req.targets.length > 1;
    result.req.targets.forEach((target, row) => {
      let error = result.dbErrors[target.cardId];   // 名刺のレコードIDからエラー情報を取得
      if (error) {
        // エラーだったとき
        lines.push(<p key={row}>{ bulkMode ? `${row+1}行目: ` : "" }{error.text}</p>);
        if (error.duplicate && error.mapId2Name ) {
          let links = [];
          Object.keys(error.mapId2Name).forEach((k, idx) => {
            links.push(<li className="slds-item"><a key={{row}-{idx}} onClick={ this.gotoRecord.bind(this, k) } >{error.mapId2Name[k]}</a></li>);
          });
          lines.push(
            <div className="slds-p-left--small">
              <ul className="slds-list--horizontal slds-has-dividers--left">
                { links }
              </ul>
            </div>);
        }
      }
    });
    return <div className="slds-m-left--x-small">{ lines }</div>;
  }

  // アカウント、コンタクト の表示用登録情報を作成
  makeAccountRegistInfo() {
    const accoutnOverwriteSecsion = this.props.svconfig.registDispAccountOverWriteCheckbox ? classnames("slds-text-body--regular slds-m-top--x-small") : classnames("slds-hide");  // 2017.06.xx v1.20, 2018.03.xx v1.22
    const contactOverwriteSecsion = this.props.svconfig.registDispContactOverWriteCheckbox ? classnames("slds-text-body--regular slds-m-top--x-small") : classnames("slds-hide");  // 2017.06.xx v1.20, 2018.03.xx v1.22
    // Account 登録情報
    let acntTarget;
    let acntOpe;
    if (this.props.request.target.acntId !== null) {
      // 更新
      acntTarget=
        <div className="slds-item--detail slds-truncate" style={{ fontWeight: "bolder" }}>{this.props.request.target.acntName}</div>;
      acntOpe =
          <div className={ accoutnOverwriteSecsion }>
            <Icon category="action" icon={ this.props.request.acntParam.overwrite ? "edit" : "share_link" } className="slds-icon--x-small" />
            &nbsp;{ this.props.svconfig.accountLabel + (this.props.request.acntParam.overwrite ? "の情報を上書きします。" : "の情報を上書きしません。") }
          </div>;
    }
    else {
      // 新規
      acntTarget=
        <div className="slds-item--detail slds-truncate" style={{ fontWeight: "bolder" }}>
          <Icon category="action" icon="new_account" className="slds-icon--x-small"/>&nbsp;新規作成
        </div>;
      // レコードタイプ v1.20 レコードタイプ選択表示設定反映
      if (this.props.svconfig.registDispAccountRectype && this.props.request.acntParam.recTypeId) {
        acntOpe =
          <div className="slds-list--horizontal slds-wrap slds-m-top--x-small">
            <div className="slds-item--label slds-text-color--weak slds-truncate" title="レコードタイプ">レコードタイプ:</div>
            <div className="slds-item--detail slds-truncate">{this.props.request.acntParam.recTypeName}</div>
          </div>;
      }
    }
    // Contact 登録情報
    let contTarget;
    let contOpe;
    if (this.props.request.target.contId !== null ) {
      //更新
      contTarget=
        <div className="slds-item--detail slds-truncate" style={{ fontWeight: "bolder" }}>{this.props.request.target.contName}</div>;
      contOpe =
        <div className={ contactOverwriteSecsion }>
          <Icon category="action" icon={ this.props.request.contParam.overwrite ? "edit" : "share_link" } className="slds-icon--x-small" />
          &nbsp;{ this.props.svconfig.contactLabel + (this.props.request.contParam.overwrite ? "の情報を上書きします。" : "の情報を上書きしません。")}
        </div>;
    }
    else {
      // 新規
      contTarget=
        <div className="slds-item--detail slds-truncate" style={{ fontWeight: "bolder" }}>
          <Icon category="action" icon="new_contact" className="slds-icon--x-small"/>&nbsp;新規作成
        </div>;
      // レコードタイプ v1.20 レコードタイプ選択表示設定反映
      if (this.props.svconfig.registDispContactRectype && this.props.request.contParam.recTypeId) {
        contOpe =
          <div className="slds-list--horizontal slds-wrap slds-m-top--x-small">
            <div className="slds-item--label slds-text-color--weak slds-truncate" title="レコードタイプ">レコードタイプ:</div>
            <div className="slds-item--detail slds-truncate">{this.props.request.contParam.recTypeName}</div>
          </div>;
      }
    }

    let result =
      <div>
        <div className="slds-list--horizontal slds-wrap slds-m-top--xx-small">
          <div className="slds-item--label slds-text-color--weak slds-truncate" title={ this.props.svconfig.accountLabel }>
            <Icon category="standard" icon="account" className="slds-icon--x-small"/>&nbsp;{ this.props.svconfig.accountLabel }:
          </div>
          { acntTarget }
        </div>
        <div className="slds-grid slds-m-left--xx-large">
          <div className="slds-col">{ acntOpe }</div>
        </div>
        <div className="slds-list--horizontal slds-wrap slds-m-top--small">
          <div className="slds-item--label slds-text-color--weak slds-truncate" title={ this.props.svconfig.contactLabel }>
            <Icon category="standard" icon="contact" className="slds-icon--x-small"/>&nbsp;{ this.props.svconfig.contactLabel }:
          </div>
          { contTarget }
        </div>
        <div className="slds-grid slds-m-left--xx-large">
          <div className="slds-col">{ contOpe }</div>
        </div>
      </div>;
    return result;
  }

  // リード、キャンペーン の表示用登録情報を作成
  makeLeadRegistInfo() {
    // Lead 情報 lx 2.0
    const leadOverwriteSecsion = this.props.svconfig.registDispLeadOverWriteCheckbox ? classnames("slds-text-body--regular slds-m-top--x-small") : classnames("slds-hide");
    // リード 登録情報
    let leadTarget;
    let leadOpe;
    if (this.props.request.target.leadId !== null ) {
      //更新
      leadTarget=
        <div className="slds-item--detail slds-truncate" style={{ fontWeight: "bolder" }}>{this.props.request.target.leadName}</div>;
      leadOpe =
        <div className={ leadOverwriteSecsion }>
          <Icon category="action" icon={ this.props.request.leadParam.overwrite ? "edit" : "share_link" } className="slds-icon--x-small" />
          &nbsp;{ this.props.svconfig.labels.lead.objName + (this.props.request.leadParam.overwrite ? "の情報を上書きします。" : "の情報を上書きしません。")}
        </div>;
    }
    else {
      // 新規
      leadTarget=
        <div className="slds-item--detail slds-truncate" style={{ fontWeight: "bolder" }}>
          <Icon category="action" icon="new_lead" className="slds-icon--x-small"/>&nbsp;新規作成
        </div>;
      // レコードタイプ
      if (this.props.svconfig.registDispLeadRectype && this.props.request.leadParam.recTypeId) {
        leadOpe =
          <div className="slds-list--horizontal slds-wrap slds-m-top--x-small">
            <div className="slds-item--label slds-text-color--weak slds-truncate" title="レコードタイプ">レコードタイプ:</div>
            <div className="slds-item--detail slds-truncate">{this.props.request.leadParam.recTypeName}</div>
          </div>;
      }
    }
    // キャンペーン
    let campaignOption;
    let campaignMemberStatus;
    const statusLabel = this.props.svconfig.labels.campaignMember ? this.props.svconfig.labels.campaignMember.statusLabel : 'キャンペーンメンバーの状況';
    if (this.props.request.target.leadId === null && this.props.request.campaignParam.campaignId !== null) {
      campaignOption =
        <div className="slds-item--detail slds-truncate" style={{ fontWeight: "bolder" }}>{ this.props.request.campaignParam.campaignName }</div>;
      campaignMemberStatus =
          <div className="slds-list--horizontal slds-wrap slds-m-top--x-small">
            <div className="slds-item--label slds-text-color--weak slds-truncate" title={ statusLabel }>{ statusLabel }:</div>
            <div className="slds-item--detail slds-truncate">{ this.props.request.campaignParam.statusObj.Label }</div>
          </div>;
    }

    let campaignSecsion;
    if (this.props.request.target.leadId === null && this.props.request.campaignParam.campaignId !== null) {
      campaignSecsion =
        <div>
        <div className ="slds-list--horizontal slds-wrap slds-m-top--small">
          <div className="slds-item--label slds-text-color--weak slds-truncate" title={ this.props.svconfig.labels.campaign.objName }>
            <Icon category="standard" icon="campaign" className="slds-icon--x-small"/>&nbsp;{ this.props.svconfig.labels.campaign.objName }:
          </div>
          { campaignOption }
        </div>
        <div className="slds-grid slds-m-left--xx-large">
          <div className="slds-col">{ campaignMemberStatus }</div>
        </div>
        </div>;
    }


    //
    let result =
      <div>
        <div className="slds-list--horizontal slds-wrap slds-m-top--xx-small">
          <div className="slds-item--label slds-text-color--weak slds-truncate" title={ this.props.svconfig.labels.lead.objName }>
            <Icon category="standard" icon="lead" className="slds-icon--x-small"/>&nbsp;{ this.props.svconfig.labels.lead.objName }:
          </div>
          { leadTarget }
        </div>
        <div className="slds-grid slds-m-left--xx-large">
          <div className="slds-col">{ leadOpe }</div>
        </div>
        { campaignSecsion }
      </div>;
    return result;
  }

  onChangeOwnerObject(name, e, value) {
    // this.setState({ [name]: value });     //
    if (name === "ownerObject") {
      this.setState({ ownerObject: e.target.value });
    }
  }


  render() {
    if(this.props.loading || !this.props.svconfig || !this.props.request) {
      return <div></div>
    }

    const { createTodo, subject, activityDate, comment, } = this.props.request.todoParam;
    const { todoLabel, todoLabelSubject, todoLabelActivityDate, todoLabelDescription, todoLabelCreate,
             accountLabel, contactLabel, labels  } = this.props.svconfig;

    const styles = { padding: '20px' };
    const today = new Date();
    // this.props.changeTodoParam({ activityDate: today });
    // const overwriteSecsion = this.props.svconfig.registDispOverWriteCheckbox ? classnames("slds-text-body--regular slds-m-top--x-small") : classnames("slds-hide");  // 2017.06.xx v1.20
    let registOption;    // アカウントまたはリード について および コンタクトまたはキャンペーンについて 登録情報
    let lookupOwner;  //  所有者選択 その他 で 所有者をルックアップするところ
    let showOwnerDiv; // 所有者 選択を表示する？
    if (this.props.request.target.pType !== "2") {
      // アカウント、コンタクト の表示用登録情報を作成
      registOption = this.makeAccountRegistInfo();
      //
      lookupOwner =
              <LookupSObject ref="lookup" sobject="User" onSelectHandler={ this.onLookupChange.bind(this) }
                  disabled={ false } selected={ this.state.lookupedOwner }/>;
      showOwnerDiv = this.props.request.target.acntId === null; // アカウント新規
    }
    else {
      // リード、キャンペーンについて表示する。
      registOption = this.makeLeadRegistInfo();
      let opts = [<Option key="0" value="User">ユーザ</Option>, <Option key="1" value="Queue">キュー</Option>];
      lookupOwner =
        <Row>
          <Col cols={2}>
            <Select ref="lookupObj" className="slds-text-body--regular"
              label=""
              onChange={ this.onChangeOwnerObject.bind(this, 'ownerObject') }
              defaultValue={ this.state.ownerObject } >
            { opts }
            </Select>
          </Col>
          <Col cols={10}>
            <LookupSObject ref="lookup" sobject={ this.state.ownerObject } onSelectHandler={ this.onLookupChange.bind(this) }
                  disabled={ false } selected={ this.state.lookupedOwner }/>
          </Col>
        </Row>;
      showOwnerDiv = this.props.request.target.leadId === null;  //リード新規？
    }

    // 　所有者
    // this.props.selectRecordOwner(0, this.props.svconfig.user);    // 実行ユーザを所有者
    // 名刺所有者は選択不可?
    let disableCardOwner = !this.props.registOptions.card.ownerActive || this.props.registOptions.card.ownerId === this.props.svconfig.userId;
    let ownerDiv = classnames("slds-box", "slds-theme--shade", showOwnerDiv ? null : "slds-hide");

    let saveLabel = this.props.request.allowRetry ? "保存（アラートを無視）" : "保存";

    // 登録実行して重複エラーで リトライ可能なのとき
    let retryForm;
    if (this.props.request.response && this.props.request.response.allowRetry && this.props.request.response.allowRetry === true) {
      let errorinfo = this.makeApexResultInfo(this.props.request.response);
      retryForm = <div className="slds-box slds-theme--shade slds-m-bottom--small">
          <div class="slds-grid">
            <div class="slds-col">
              <Icon category="utility" icon="warning" className="slds-icon--x-small"/>
            </div>
            <div class="slds-col">
              { errorinfo }
            </div>
          </div>
          <div className="slds-m-vertical--xx-small slds-has-divider--top"></div>
          <Form type='compound'>
            <CheckboxGroup label=''>
              <Checkbox label="保存（アラートを無視）" value="2" checked={ this.state.allowSave } value={ false }
                  onClick={ this.onFieldChange.bind(this, 'allowSave') } />
            </CheckboxGroup>
          </Form>
        </div>
    }
    moment.locale('ja');    // DatePicker を日本語表示にする。
    return (
      <div style={ styles }>
        {/* リトライ情報 */}
         { retryForm }
        {/* 登録情報 */}
        <div className="slds-box slds-theme--shade">
          {/*<h3 className="slds-section-title">以下の内容で登録します。</h3>*/}
          {/*アカウント または リード*/}
          { registOption }
          {/*<div className="slds-list--horizontal slds-wrap slds-m-top--xx-small">
            <div className="slds-item--label slds-text-color--weak slds-truncate" title={ accountLabel }>
              <Icon category="standard" icon='account' className="slds-icon--x-small"/>&nbsp;{ accountLabel }:
            </div>
            {acntTarget}
          </div>
          <div className="slds-grid slds-m-left--xx-large">
            <div className="slds-col">{ acntOpe }</div>
          </div>
          <div className="slds-list--horizontal slds-wrap slds-m-top--small">
            <div className="slds-item--label slds-text-color--weak slds-truncate" title={ contactLabel }>
              <Icon category="standard" icon='contact' className="slds-icon--x-small"/>&nbsp;{ contactLabel }:
            </div>
            {contTarget}
          </div>
          <div className="slds-grid slds-m-left--xx-large">
            <div className="slds-col">{ contOpe }</div>
          </div>*/}
        </div>

        {/* 間隔をおく */}
        <div className="slds-m-vertical--small"/>

        {/* Owner */}
        <div className={ ownerDiv }>
          <Row>
          <div className="slds-grid slds-wrap">
            <div className="slds-col slds-small-size--1-of-6 slds-medium-size--2-of-12 slds-large-size--2-of-12">
              <div title="所有者"
                onMouseEnter={() => this.popover.toggle(true)} onMouseLeave={() => this.popover.toggle(false)} >
                <Icon category="standard" icon="user" className="slds-icon--x-small"/>&nbsp;所有者:
              </div>
              <Popover
                ref={ ref => (this.popover = ref) }
                position='top-left'
                tooltip
                theme='info'
                style={{
                  position: 'absolute',
                }}>
                <div className="slds-text-body--regular">新規作成時の所有者を指定します。</div>
              </Popover>
            </div>
            <div className="slds-col slds-small-size--3-of-6 slds-medium-size--4-of-12 slds-large-size--5-of-12">
            {/*<div className="slds-col slds-align-middle">*/}
              <RadioGroup2 name='ownerChoice' onChange={ this.onFieldChange.bind(this, 'ownerChoice') }>
                <Row >
                  <Radio label='私' value={ 0 } checked={ this.state.ownerChoice === 0 } defaultChecked />
                  <Radio label='名刺の所有者' value={ 1 } checked={ this.state.ownerChoice === 1 } disabled={ disableCardOwner }/>
                  <Radio label='その他' value={ 2 } checked={ this.state.ownerChoice === 2 }  />
                </Row>
              </RadioGroup2>
            </div>
            {/* その他 所有者 の ルックアップ */}
            <div className="slds-col slds-small-size--2-of-6 slds-medium-size--6-of-12 slds-large-size--5-of-12">
              { lookupOwner }
            </div>
          </div>

          {/*<div className="slds-list--horizontal slds-wrap">
            <div className="slds-item--label slds-text-color--weak slds-truncate" title="所有者">
              <Icon category="standard" icon='user' className="slds-icon--x-small"/>&nbsp;所有者:
            </div>
            <div className="slds-item--detail slds-p-right--xx-large">
              <LookupSObject ref="lookup" sobject="User" onSelectHandler={ this.onLookupChange.bind(this) }/>
            </div>
          </div>*/}
          </Row>
        </div>

        {/* 間隔をおく */}
        <div className="slds-m-vertical--small"/>

        {/* ToDo */}
        <div className="slds-box slds-theme--shade">
          <Form type='compound'>
            <CheckboxGroup label=''>
              <Checkbox label={ todoLabelCreate } value="2" checked={ createTodo } value={ true }
                  onClick={ this.onFieldChange.bind(this, 'createTodo') } />
            </CheckboxGroup>
            <Input label={ todoLabelSubject } type='text' placeholder={ todoLabel+' ' + todoLabelSubject } value={ subject }
              onChange={ this.onFieldChange.bind(this, 'subject') }/>
            <div style={{ maxWidth: '200px' }}>
              <DateInput label={ todoLabelActivityDate } placeholder='YYYY/MM/DD' dateFormat='YYYY/MM/DD'
                  defaultValue={ today } value={ activityDate }
                  onValueChange={ value => this.onFieldChange('activityDate', {}, value) }
                  extensionRenderer={ TodayButtonExtensionRenderer }
                  style={{ zIndex: 9001 }}/>
            </div>
            <Textarea label={ todoLabelDescription } defaultValue='' placeholder='ToDo の コメント' value={ comment }
              onChange={ this.onFieldChange.bind(this, 'comment') }/>
          </Form>
        </div>
        {/* フッターの分だけスペースをおく */}
        <div style={{ marginBottom: '48px' }}></div>
        {/* フッター */}
        <Footer left={{ label:"戻る", icon: "back", func: this.props.goBack }}
                center={{ label:"Cancel", icon: "ban", func: this.props.cancel }}
                right={{ label: saveLabel, icon: "check", func: this.goOn.bind(this) }} />
      </div>
    );
  }
}


export default RegistConfirm;



