// LookupTarget.js
//  オブジェクトをルックアップして選択された オブジェクトの RegistOption をセットする。
import React, { Component, PropTypes } from 'react';
import { Link } from 'react-router';
import { Form, Lookup } from '../rlds';//'react-lightning-design-system';
import { searchRegistOptions } from '../api/sfdc';

// SOSL する
function searchData(searchText, objname, accountId, recTypeIds, callback) {
  searchRegistOptions(searchText, objname, accountId, recTypeIds).then((response) => {
    if (!response.error) {
      callback(response);
    }
    else {
      console.log('searchRegistOptions: ' + response);
    }
  });
}

// 本体
class LookupTarget extends Component {
  constructor(props) {
    super(props);
    this.state = {
      data: [],
      searchText: '',
      selected: this.props.registOption.lookuped ? this.props.registOption.lookuped : null,
      loading: false,
      opened: false,
    };
  }

  onSearchTextChange(searchText) {
    this.setState({ searchText });
  }

  onLookupRequest(searchText = '') {
    this.setState({ data: [], loading: true, opened: true });
    let icon = 'standard:' + this.props.sobject.toLowerCase();
    searchData(searchText, this.props.sobject, this.props.accountId, this.props.recTypeIds, (response) => {
      let data = [];
      response.forEach((item, idx) => { data.push(
        { icon: icon,
          label: item.title + ' (' + item.summary.toString() +')',
          value: item.id,
          scope: this.props.sobject,
        });
      });
      this.setState({ data, loading: false });
    });
  }

  render() {

    return (
      <Form type='stacked'>
          <Lookup
            disabled={ this.props.disabled }
            opened={ this.state.opened }
            searchText={ this.state.searchText }
            selected={ this.state.selected }
            data={ this.state.data }
            loading={ this.state.loading }
            onSearchTextChange={ this.onSearchTextChange.bind(this) }
            onLookupRequest={ this.onLookupRequest.bind(this) }
            // onSelect={ selected => this.setState({ selected }) }
            onSelect={ selected => {
                this.props.onSelectHandler(selected);
                this.setState({ selected });
              }
            }
            onComplete={ () => this.setState({ opened: false }) }
          />
      </Form>
    );
  }
}

LookupTarget.propTypes = {
  disabled: PropTypes.bool,
  sobject: PropTypes.string.isRequired,
  accountId: PropTypes.string,
  recTypeIds: PropTypes.arrayOf(PropTypes.string),
  onSelectHandler: PropTypes.func,
};

export default LookupTarget;



