// 選択したビューに該当するレコードを取得 Pagenation 対応
export function getFilteredRecords(req_param) {
  if (!inVF) {
    return new Promise((resolve, reject) => { setTimeout(() => resolve(FILTEREDRECORDS), 50)});
  }
  return new Promise((resolve, reject) => {
    var param = {
      apexType: (namespace!='' ? namespace + '.' : '') + 'SB_NCL_TwowayRegistController.FilteredQueryRequest',
      obj_name: 'NameCard__c',
      fields: 'Name, company_name__c, division__c, title_name__c',
      order: 'Id',
      isAsc: false,
      filterId: req_param.viewId,   // リストビューのID
      // 'filterId' : '00B1000000443gFEAQ',   // リストビューのID
      pageNo: 1,
      pageSize: 100
    };

    Visualforce.remoting.Manager.invokeAction(
      (namespace!='' ? namespace + '.' : '') + 'SB_NCL_TwowayRegistController.getFilteredRecords',
      param,
      function(result, event) {
        if (event.status) {
          return resolve(result.records);
        }
        else {
          const res = { title: "接続エラー", level: "error", message: event.message, sfdcevent: event };
          console.log(res); // event : { statusCode: 400, type: "exception", message: "Attempt to de-reference a null object", ... }
          return reject(res);
        }
      },
      { escape: false }
    );
  });
}

