// 登録先オプションの取得 複数名刺対応
export function getRegistOptions(req_param) {
  if (!inVF) {
    if (req_param.querySettings.bulkRegist) {
      return new Promise((resolve, reject) => { setTimeout(() => resolve(BULKREGISTOPTIONS), 50)});
    }
    else {
      return new Promise((resolve, reject) => { setTimeout(() => resolve(REGISTOPTIONS), 50)});
    }
  }
  return new Promise((resolve, reject) => {
    var opt = {
      apexType: (namespace!='' ? namespace + '.' : '') + 'SB_NCL_TwowayRegistController.QueryOption',
      bulkRegist: true,  //  一括登録か
      querySite: req_param.querySettings ? req_param.querySettings.querySite : false,
      queryState: req_param.querySettings ? req_param.querySettings.queryState : false,
      queryCity: req_param.querySettings ? req_param.querySettings.queryCity : false,
      queryZip: req_param.querySettings ? req_param.querySettings.queryZip : false,
      acntRecTypeIds: req_param.querySettings ? req_param.querySettings.acntRecTypeIds : null,
      contRecTypeIds: req_param.querySettings ? req_param.querySettings.contRecTypeIds : null
    };
    var req = {
      apexType: (namespace!='' ? namespace + '.' : '') + 'SB_NCL_TwowayRegistController.RegistOptionRequest',
      opt: opt,
      ncIds: req_param.cardIds,
    };

    Visualforce.remoting.Manager.invokeAction(
      (namespace!='' ? namespace + '.' : '') + 'SB_NCL_TwowayRegistController.getRegistOptions',
      req,
      function(result, event) {
        if (event.status) {
          return resolve(result);
        }
        else {
          const res = { title: "接続エラー", level: "error", message: event.message, sfdcevent: event };
          console.log(res); // event : { statusCode: 400, type: "exception", message: "Attempt to de-reference a null object", ... }
          return reject(res);
        }
      },
      { escape: false }
    );
  });
}

