// 名刺に該当する取引先、リード候補を取得
export function getAccountOptions(cardId, querySettings) {
  if (!inVF) {
    return new Promise((resolve, reject) => { setTimeout(() => resolve(ACCOUNTOPTIONS), 50)});
  }
  return new Promise((resolve, reject) => {
    var opt = {
      apexType: (namespace!='' ? namespace + '.' : '') + 'SB_NCL_TwowayRegistController.QueryOption',
      registTarget:querySettings ? querySettings.registTarget : -1,
      bulkRegist: false,
      querySite: querySettings ? querySettings.querySite : false,
      queryState: querySettings ? querySettings.queryState : false,
      queryCity: querySettings ? querySettings.queryCity : false,
      queryZip: querySettings ? querySettings.queryZip : false,
      queryPersonEMail: querySettings ? querySettings.queryPersonEMail : true,  // lx 2.0
      queryPersonName: querySettings ? querySettings.queryPersonName : false, // lx 2.0
      acntRecTypeIds: (querySettings && querySettings.acntRecTypeIds ? querySettings.acntRecTypeIds : null),
      contRecTypeIds: (querySettings && querySettings.contRecTypeIds ? querySettings.contRecTypeIds : null), // v2.0.2 2018.08.0x SV_DEV-1049 LEX 取引先登録 個別 画面で、取引先レコードタイプの条件を変更すると、「接続エラー」
      leadRecTypeIds: (querySettings && querySettings.leadRecTypeIds ? querySettings.leadRecTypeIds : null) // lx 2.0
    };
    Visualforce.remoting.Manager.invokeAction(
      (namespace!='' ? namespace + '.' : '') + 'SB_NCL_TwowayRegistController.getAccountOptions',
      cardId, opt,
      function(result, event) {
        if (event.status) {
          return resolve(result);
        }
        else {
          const res = { title: "接続エラー", level: "error", message: event.message, sfdcevent: event };
          console.log(res); // event : { statusCode: 400, type: "exception", message: "Attempt to de-reference a null object", ... }
          return reject(res);
        }
      },
      { escape: false }
    );
  });
}

