// SOSL で 所定のオブジェクトを検索して RegistOption で返す 親のアカウントのIDを指定可能
export function searchRegistOptions(word, objName, accountId, recTypes) {
  if (!inVF) {
    if (objName === 'Account') {
      return new Promise((resolve, reject) => { setTimeout(() => resolve(SEARCHACCOUNT), 50)});
    }
    else if (objName === 'Contact') {
      return new Promise((resolve, reject) => { setTimeout(() => resolve(SEARCHCONTACT), 50)});
    }
  }
  return new Promise((resolve, reject) => {
    Visualforce.remoting.Manager.invokeAction(
      (namespace!='' ? namespace + '.' : '') + 'SB_NCL_TwowayRegistController.searchRegistOptions',
      word, objName,
      (accountId ? accountId : null),
      (recTypes ? recTypes : null),
      function(result, event) {
        if (event.status) {
          return resolve(result);
        }
        else {
          const res = { title: "接続エラー", level: "error", message: event.message, sfdcevent: event };
          console.log(res); // event : { statusCode: 400, type: "exception", message: "Attempt to de-reference a null object", ... }
          return reject(res);
        }
      },
      { escape: false }
    );
  });
}

// SOSL で 所定のオブジェクトを検索して RegistOption で返す 親のアカウントのIDを指定可能
export function searchSObjects(word, objName, recTypes) {
  if (!inVF) {
    if (objName === 'User') {
      return new Promise((resolve, reject) => { setTimeout(() => resolve(SEARCHUSER), 50)});
    }
    else {
      return null;
    }
  }
  return new Promise((resolve, reject) => {
    Visualforce.remoting.Manager.invokeAction(
      (namespace!='' ? namespace + '.' : '') + 'SB_NCL_TwowayRegistController.searchSObjects',
      word, objName,
      (recTypes ? recTypes : null),
      function(result, event) {
        if (event.status) {
          return resolve(result);
        }
        else {
          const res = { title: "接続エラー", level: "error", message: event.message, sfdcevent: event };
          console.log(res); // event : { statusCode: 400, type: "exception", message: "Attempt to de-reference a null object", ... }
          return reject(res);
        }
      },
      { escape: false }
    );
  });
}

// 登録実行
export function registAccountContact(req_param) {
  if (!inVF) {
    return new Promise((resolve, reject) => { setTimeout(() => resolve(REGISTACCOUNTCOUNT), 50)});
    // エラー
    // const res = { title: "接続エラー", level: "error", message: SYSTEM_ERROR01.message, sfdcevent: SYSTEM_ERROR01 };
    // return new Promise((resolve, reject) => { setTimeout(() => reject(res), 50)});
    // 警告
    // return new Promise((resolve, reject) => { setTimeout(() => resolve(APEX_ERROR01), 50)});

  }

  return new Promise((resolve, reject) => {
    // let newAcnt = false;　 // 新規アカウントが１つでもあるか
    // let newCont = false;  // 新規コンタクトが１つでもあるか
    let targets = [];
    if (req_param.targets) {
      // Bulk
      req_param.targets.forEach(function(t){
        let target =  {
          apexType: (namespace!='' ? namespace + '.' : '') + 'SB_NCL_TwowayRegistController.RegistTarget',
          cardId: t.cardId,
          acntId: t.acntId || null,
          contId: t.contId || null,
          leadId: t.leadId || null,     // lx 2.0
          contCampaignId: null,     // lx 2.0
          contCampaignStatus: null,     // lx 2.0
          leadCampaignId: null,     // lx 2.0
          leadCampaignStatus: null,     // lx 2.0
          createTodo: t.createTodo,
          todoDate: t.todoDate.toUTCString(),
        };
        targets.push(target);
        // newAcnt = newAcnt || t.acntId==null;
        // newCont = newCont || t.contId==null;
      });
    }
    else {
      // 個別
      let target =  {
        apexType: (namespace!='' ? namespace + '.' : '') + 'SB_NCL_TwowayRegistController.RegistTarget',
        pType2: Number(req_param.target.pType), // lx 2.0　1: Account系 2:Lead系
        cardId: req_param.target.cardId,
        acntId: req_param.target.acntId,
        contId: req_param.target.contId,
        leadId: req_param.target.leadId, // lx 2.0
        contCampaignId: null,           //  コンタクトを キャンペーンメンバーにする。
        contCampaignStatus: null,          //  コンタクト キャンペーンメンバーのステータス
        leadCampaignId: req_param.campaignParam.campaignId,          //  リード を キャンペーンメンバーにする。
        leadCampaignStatus: req_param.campaignParam.statusObj ? req_param.campaignParam.statusObj.Label : null,          //  リード キャンペーンメンバーのステータス
        createTodo: req_param.todoParam.createTodo,   // TRUE： ToDoを作る
        todoDate: req_param.todoParam.activityDate.toUTCString(),   // ToDoの日付
      };
      targets.push(target);
      // newAcnt = req_param.target.acntId==null;
      // newCont = req_param.target.contId==null;
    }
    let acntParam =  {
      apexType: (namespace!='' ? namespace + '.' : '') + 'SB_NCL_TwowayRegistController.RegistParam',
      objName: 'Account',
      overwrite: req_param.acntParam.overwrite,//(newAcnt ? false : req_param.acntParam.overwrite),
      recTypeId: req_param.acntParam.recTypeId,// (newAcnt && req_param.acntParam.recTypeId ? req_param.acntParam.recTypeId : null),
      ownerId: req_param.owner.user ? (req_param.owner.user.id || req_param.owner.user.value) : null,  // バルクではNULL。ラジオボタンでは id、 LOOKUP したら value
      allowSave: req_param.acntParam.allowSave,
    };
    let contParam =  {
      apexType: (namespace!='' ? namespace + '.' : '') + 'SB_NCL_TwowayRegistController.RegistParam',
      objName: 'Contact',
      overwrite: req_param.contParam.overwrite,//(newCont ? false : req_param.contParam.overwrite),
      recTypeId: req_param.contParam.recTypeId, //(newCont && req_param.contParam.recTypeId ? req_param.contParam.recTypeId : null),
      ownerId: req_param.owner.user ? (req_param.owner.user.id || req_param.owner.user.value) : null,  // バルクではNULL。ラジオボタンでは id、 LOOKUP したら value
      allowSave: req_param.contParam.allowSave,
    };
    let leadParam =  {  // lx 2.0
      apexType: (namespace!='' ? namespace + '.' : '') + 'SB_NCL_TwowayRegistController.RegistParam',
      objName: 'Lead',
      overwrite: req_param.leadParam.overwrite,//(newCont ? false : req_param.contParam.overwrite),
      recTypeId: req_param.leadParam.recTypeId, //(newCont && req_param.contParam.recTypeId ? req_param.contParam.recTypeId : null),
      ownerId: req_param.owner.user ? (req_param.owner.user.id || req_param.owner.user.value) : null,  // バルクではNULL。ラジオボタンでは id、 LOOKUP したら value
      allowSave: req_param.leadParam.allowSave,
    };
    let todoParam = {
      apexType: (namespace!='' ? namespace + '.' : '') + 'SB_NCL_TwowayRegistController.TodoParam',
      subject: req_param.todoParam.subject,
      activityDate: req_param.todoParam.activityDate.toUTCString(),
      comment: req_param.todoParam.comment,
      // SV_DEV-715 「取引先・取引先責任者に登録」の更新時のTodoの任命先は取引先責任者の所有者にする
      ownerId: null,  // NULL で 新規も更新も ToDo は コンタクトの所有者
    };
    var req = {
      apexType: (namespace!='' ? namespace + '.' : '') + 'SB_NCL_TwowayRegistController.RegistRequest',
      targets: targets,
      acntParam: acntParam,
      contParam: contParam,
      leadParam: leadParam,
      todoParam: todoParam,
    };
    Visualforce.remoting.Manager.invokeAction(
      (namespace!='' ? namespace + '.' : '') + 'SB_NCL_TwowayRegistController.registAccountContact',
      req,
      function(result, event) {
        if (event.status) {
          return resolve(result);
        }
        else {
          console.log(event); // event : { statusCode: 400, type: "exception", message: "Attempt to de-reference a null object", ... }
          const res = { title: "接続エラー", level: "error", message: event.message, sfdcevent: event };
          return reject(res);
        }
      },
      { escape: false }
    );
  });
}



