// RegistConfirmContainer.js
import React, { Component } from 'react';
import { connect } from 'react-redux';
import { changeStep, setRecordOwner, setTodoParam,
  doRegistAccountContact, doRegistAccountContactSuccess, doRegistAccountContactFailure,
  addError, setAllowRetry, clearResponse, } from '../actions/index';
import RegistConfirm from '../components/RegistConfirm.js';

const mapStateToProps = (state, ownProps) => {
  const { step, loading, ...process } = state.get('process').toObject();
  const svconfig = state.getIn(['config', 'svconfig']);
  const registOptions = state.get('registOptions').toObject();
  const request = state.get('request').toObject();
  // const todoParam = state.getIn(['request', 'todoParam']);
  const res = { svconfig, registOptions, request,
            location: ownProps.router.location, step, loading, };
  return res;
}

const mapDispatchToProps = (dispatch, ownProps) => {
  return {
    // 現在のステップを設定
    setCurrentStep: (step) => {
      dispatch(changeStep(step));
    },
    // 所有者の選択
    selectRecordOwner: (choice, user) => {
      dispatch(setRecordOwner({choice, user}));
    },
    // ToDo の値を Store に保存する
    changeTodoParam: (param) => {
      dispatch(setTodoParam(param));
    },
    // 戻る
    goBack: (event) => {
      event.preventDefault();
      let query;
      // if (ownProps.router.location.query.pType !== 2) {
      //   query = {
      //     pType: 1,
      //     aid: ownProps.router.location.query.aid,
      //     cid: ownProps.router.location.query.cid,
      //     prev: '/confirm',
      //   };
      // }
      // else {
      //   query = {
      //     pType: 2,
      //     // lid: ownProps.router.location.query.lid,
      //     // campid: ownProps.router.location.query.campid,
      //     prev: '/confirm',
      //   };
      // }
      ownProps.router.push({
        pathname: ownProps.router.location.query.prev,
        query: {
          pType: ownProps.router.location.query.pType,
          prev: "/confirm",
        }
      });
    },
    // レコード詳細へ
    cancel: (event) => {
      event.preventDefault();
      if( (typeof sforce != "undefined") && (sforce != null) ) {
        // S1, LEX
        sforce.one.navigateToURL("/" + vfValue.targetId);
      } else {
        // クラシック
        window.open("/" + vfValue.targetId, "_top");
      }
    },

    // レコードへ
    goto: (recId) => {
      if( (typeof sforce != "undefined") && (sforce != null) ) {
        // S1, LEX
        sforce.one.navigateToURL("/" + recId);
      } else {
        // クラシック
        window.open("/" + recId, "_top");
      }
    },

    // 登録実行
    goOn: (request, allowSave) => {
      let req_param;
      req_param = Object.assign({}, request, { response: null });
      if (allowSave === true) {
        if (request.response && request.response.allowRetry && request.response.process) {
          if (request.response.process === "contact" || request.response.process === "account") {
            req_param.acntParam = Object.assign({}, request.acntParam, { allowSave: true });
            if (request.response.process === "contact") {
              req_param.contParam = Object.assign({}, request.contParam, { allowSave: true });
            }
          }
          else if (request.response.process === "lead") {
            req_param.leadParam = Object.assign({}, request.contParam, { allowSave: true });
          }
        }
      }
      dispatch(doRegistAccountContact(req_param)).then((response) => {
        if (!response.error) {
          dispatch(doRegistAccountContactSuccess(response.payload));
          if (response.payload.success) {
            let query = Object.assign({}, ownProps.router.location.query, { prev: "/confirm" });
            // 正常終了、登録成功
            ownProps.router.push({
              pathname: "/fin",
              query
            });
            // // 遷移先ページから histroy.backすると 最初のページが表示される
            // //  Edgeだと、リロードしまくり現象が発生するので、ためしに直接登録したレコードの詳細ページに飛んで見る。
            // if( (typeof sforce != 'undefined') && (sforce != null) ) {
            //   // S1, LEX
            //   sforce.one.navigateToURL('/' + ownProps.router.location.query.cid);
            // } else {
            //   // クラシック
            //   window.open('/' + ownProps.router.location.query.cid, "_top");
            // }
          }
          else {
            // 正常終了で、登録の失敗のとき
            // let message = response.payload.messages && Array.isArray(response.payload.messages) ? response.payload.messages.join("<BR>") : "";
            let message = response.payload.message || "登録に失敗しました。";
            const content = { title: response.payload.title, level: "warning", message, apexresult: response.payload, goback:false };
            // const notify =  true;
            dispatch(addError(content));
            // dispatch(showModalNotification(true));
            // 正常終了で、登録の失敗のとき
            if (response.payload.allowRetry) {
              dispatch(setAllowRetry(true));
            }
          }
        }
        else {
          dispatch(doRegistAccountContactFailure(response.payload));
        }
      });
    },
    clearResponse: () => {
      dispatch(clearResponse());
    },
  }

}

export default connect(mapStateToProps, mapDispatchToProps)(RegistConfirm);



