// Collapsible.js
import React, { Component, PropTypes } from 'react';
import classnames from 'classnames';

class Collapsible extends Component {
  constructor(props) {
    super(props);
    this.state = {
      opened: false,
    };
  }

  toggleOpen(e) {
    if (e) {
      e.preventDefault();
    }
    this.setState ({
      opened: !this.state.opened,
    });
  }

  render() {

    let collpaseClass = classnames(this.state.opened ? "slds-is-expanded" : "slds-is-collapsed");
    let title = this.state.opened ? this.props.title.close : this.props.title.open;

    return (
      <div >
        <a className="slds-text-title" onClick={ this.toggleOpen.bind(this) } > { title } </a>
        <div className={ collpaseClass }>
          { this.props.children }
        </div>
      </div>
    );
  }
}

Collapsible.propTypes = {
  title: PropTypes.shape({
    open: PropTypes.string.isRequired,
    close: PropTypes.string.isRequired,
  }),
};

export default Collapsible;


