// RegistContactContainerjs
import React, { Component } from 'react';
import { connect } from 'react-redux';
import { changeStep,
      fetchContactOptions, fetchContactOptionsSuccess, fetchContactOptionsFailure,
      selectContactOption,
      setRegistTarget, setContactParam, setOverwrite } from '../actions/index';
import RegistContact from '../components/RegistContact.js';

const mapStateToProps = (state, ownProps) => {
  const { step, loading, ...process } = state.get('process').toObject();
  const svconfig = state.getIn(['config', 'svconfig']);
  const request = state.get('request').toObject();
  const contactOptions = state.getIn(['registOptions', 'contactOptions']);
  const recTypes = state.getIn(['config', 'recTypes']);
  const card = state.getIn(['registOptions', 'card']);
  const querySettings = state.getIn(['config', 'querySettings']);
  const res = { svconfig,
            cardId: card.id, sobjectname: 'Contact', recTypes : recTypes.Contact,
            registParam: request.contParam,
            accountId: request.target.acntId,
            registOptions: contactOptions,
            querySettings,
            location: ownProps.router.location, step, loading, };
  return res;
}

const mapDispatchToProps = (dispatch, ownProps) => {
  return {
    // 現在のステップを設定
    setCurrentStep: (step) => {
      dispatch(changeStep(step));
    },
    // 選択した名刺の取引先責任者登録候補一覧の表示
    fetchContactOptions: (accountId, querySettings) => {
      dispatch(fetchContactOptions({ cardId: vfValue.targetId, accountId, querySettings })).then((response) => {
        // let data = response.payload.data ? response.payload.data : 'Network Error';
        return !response.error ? dispatch(fetchContactOptionsSuccess(response.payload)) : dispatch(fetchContactOptionsFailure(response.payload));
      });
    },
    // アカウント新規作成のとき
    buildCreateNewOption: () => {
      let records = [
          {
            "selected" : true,
            "index" : 0,
            "info" : "取引先を新たに追加します。",
            "title" : "新規取引先責任者追加",
            "type" : 3
          },
        ];
      dispatch(fetchContactOptionsSuccess(records));
    },
    // 選択
    selectOption: (index) => {
      // let idx = event.target.value;
      dispatch(selectContactOption(index));
    },
    toggleOverwrite: (sobjectname, val) => {
      dispatch(setOverwrite({sobjectname, val}));
    },
    // 戻る
    goBack: (event) => {
      event.preventDefault();
      ownProps.router.push({
        pathname: '/',
        query: {
          aid: ownProps.router.location.query.acntid,
          // cid: ownProps.router.location.query.cid,
          prev: '/cont',
        },
      });
    },
    // レコード詳細へ
    cancel: (event) => {
      event.preventDefault();
      if( (typeof sforce != 'undefined') && (sforce != null) ) {
        // S1, LEX
        sforce.one.navigateToURL('/' + vfValue.targetId);
      } else {
        // クラシック
        window.open('/' + vfValue.targetId, "_top");
      }
    },
    // 次へ
    goOn: (opt) => {
      let contId = opt.type === 1 ? opt.id : (opt.type === 2 ? opt.lookuped.value : null);
      // 選択した対象をキープ
      dispatch(setRegistTarget({
        contId: contId,
        contName: opt.type === 1 ? opt.title : (opt.type === 2 ? opt.lookuped.label : null),
      }));
      //  レコードタイプと、上書き設定をキープ
      dispatch(setContactParam({
        recTypeId : opt.type === 3 && opt.recTypeId ? opt.recTypeId : null,
        recTypeName : opt.type === 3 && opt.recTypeName ? opt.recTypeName : null,
        allowSave : false,
      }));
      let query = Object.assign({}, ownProps.router.location.query, { contId, prev: "/cont" });
      ownProps.router.push({
        pathname: '/confirm',
        query
      });
    },

  }

}

export default connect(mapStateToProps, mapDispatchToProps)(RegistContact);



