// LookupSObject.js
//  オブジェクトをルックアップして選択された オブジェクトの RegistOption をセットする。
import React, { Component, PropTypes } from 'react';
import { Link } from 'react-router';
import { Form, Lookup } from '../rlds';//'react-lightning-design-system';
import { searchSObjects } from '../api/sfdc';

// SOSL する
function searchData(searchText, objname, recTypeIds, callback) {
  searchSObjects(searchText, objname, recTypeIds).then((response) => {
    if (!response.error) {
      callback(response);
    }
    else {
      console.log('searchRegistOptions: ' + response);
    }
  });
}

// 本体
class LookupSObject extends Component {
  constructor(props) {
    super(props);
    this.state = {
      data: [],
      searchText: '',
      selected: this.props.selected ? this.props.selected : null,
      loading: false,
      opened: false,
    };
  }

  onSearchTextChange(searchText) {
    this.setState({ searchText });
  }

  onLookupRequest(searchText = '') {
    this.setState({ data: [], loading: true, opened: true });
    let icon;
    if (this.props.sobject === 'Queue') {
     icon = 'standard:orders';  // Queue のアイコンは なくて 標準はこれをつかってるんで合わせる。
    }
    else {
     icon = 'standard:' + this.props.sobject.toLowerCase();
    }
    searchData(searchText, this.props.sobject, this.props.recTypeIds, (response) => {
      let data = [];
      response.forEach((item, idx) => { data.push(
        { icon: icon,
          label: item.Name,
          value: item.Id,
          scope: this.props.sobject,
        });
      });
      this.setState({ data, loading: false });
    });
  }

  render() {
    return (
      <Form type='stacked'>
          <Lookup
            opened={ this.state.opened }
            searchText={ this.state.searchText }
            selected={ this.state.selected }
            data={ this.state.data }
            loading={ this.state.loading }
            onSearchTextChange={ this.onSearchTextChange.bind(this) }
            onLookupRequest={ this.onLookupRequest.bind(this) }
            // onSelect={ selected => this.setState({ selected }) }
            onSelect={ selected => {
                this.props.onSelectHandler(selected);
                this.setState({ selected });
              }
            }
            onComplete={ () => this.setState({ opened: false }) }
          />
      </Form>
    );
  }
}

LookupSObject.propTypes = {
  sobject: PropTypes.string.isRequired,
  recTypeIds: PropTypes.arrayOf(PropTypes.string),
};

export default LookupSObject;



