// ModalNotificationContainer.js
import React, { Component } from 'react';
import { connect } from 'react-redux';
import { delError, } from '../actions/index';
import ModalNotification from '../components/ModalNotification.js';

const mapStateToProps = (state, ownProps) => {
  // const modalNotification = state.getIn(['process', 'modalNotification']);
  const errors = state.getIn(['process', 'errors']);
  const error = errors && Array.isArray(errors) && errors.length > 0? errors[0] : null;
  // const response = state.getIn(['request', 'response']);
  // const res = { opened: (error ? error.notify : false), content: (error ? error.content : null), errors };
  const res = { errors };
  // const res = { opened: modalNotification, content: error.content, errors };
  return res;
}

const mapDispatchToProps = (dispatch, ownProps) => {
  return {

    //  モーダルの 　show - hide
    hideModal: (goback) => {
      dispatch(delError(0));
      // if (!vfValue.targetIds || vfValue.targetIds.length == 0) {
      if (goback) {
        // 名刺を選択してません とか、設定を取得できなかったとか のとき
        if( (typeof sforce != 'undefined') && (sforce != null) ) {
          // LEX or S1
          sforce.one.back(true);
        }
        else {
          // クラシック
          window.open(vfValue.retUrl, "_top");
        }
      }
    },
    // レコードへ
    goto: (recId) => {
      if( (typeof sforce != 'undefined') && (sforce != null) ) {
        // S1, LEX
        sforce.one.navigateToURL('/' + recId);
      } else {
        // クラシック
        window.open('/' + recId, "_top");
      }
    },
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(ModalNotification);




