// RegistCampaign.js
// リード新規作成のときの キャンペーン 選択画面
import React, { Component } from 'react';
import { Link } from 'react-router';
import classnames from 'classnames';
import Footer from './Footer';
import LookupSObject from './LookupSObject';
import RegistOption from '../containers/RegistOptionContainer';
import {
  Form,
  Table,
  TableHeader,
  TableBody,
  TableRow,
  TableHeaderColumn,
  TableRowColumn,
  TableRowColumnActions,
  DropdownButton,
  MenuItem,
  RadioGroup, Radio,
  Select, Option,
  Popover,
  CheckboxGroup, Checkbox,
  Icon
} from '../rlds';

class RegistCampaign extends Component {
  constructor(props) {
    super(props);
    console.log("location:");
    console.log(this.props.location);
    let selectedIndex = -1;
    let lookupedCampaign;
    let lookupSelected;
    this.props.campaignOptions.map((val, idx) => {
      if (val.type === 2) {
        lookupedCampaign = val["lookuped"];
        lookupSelected = val.selected;
      }
      if (val.selected) {
        selectedIndex = idx;
      }
      return val;
    });
    this.state = {
      selectedInex: selectedIndex,
      lookupedCampaign,
      lookupSelected,
      'selectedCampaignMemberStatus': this.props.selectedCampaignMemberStatus,
      // campaignStatuss : null,
    };

  }

  componentWillMount() {
    if (this.props.location.query.prev === '/') {
      // キャンペーンの候補リスト取得
      this.props.fetchCampaignOptions();
      this.props.setCampaignMemberStatusS(null);
    }
  }

  componentDidMount() {
    this.props.setCurrentStep(1);
  }

  onLookupChange(value) {
    this.setState({ 'lookupedCampaign' : value });
    this.props.selectLookuped(value, this.state.lookupSelected);
    if (this.state.lookupSelected) {
      // 選択されてたら、ステータス選択肢を更新
      let campaignId = value ? value.value : null;
      this.props.setCampaignMemberStatusS(campaignId);
    }
  }

  // onChangeStatusSelect(name, e, value) {
  //   this.setState({ [name]: value });
  //   this.props.selectCampaignMemberStatus(value);
  // }
  onChangeStatusSelect(name, e, value) {
    // if (name === "recTypeId") {
      // let param = { id: value, name: e.target.options[e.target.selectedIndex].innerHTML };
      this.props.selectCampaignMemberStatus(this.props.campaignMemberStatuss[e.target.selectedIndex]);
    // }
  }

  // 選択
  onSelectOption(index, e, value) {
    // let recType = React.findDOMNode(this.refs.recType);
    console.log(this.state);
    if (this.props.campaignOptions[index].selected) {
      return;
    }
    this.setState({ 'selectedInex' : index });
    let lookupSelected = this.props.campaignOptions[index].type === 2;　// ルックアップを選んでる？
    this.setState({ lookupSelected });
    // キャンペーンを選択
    this.props.selectCampaignOption(index, this.props.campaignOptions);
    //
    let campaignId;
    if (this.props.campaignOptions[index].type === 4 ||
        (this.props.campaignOptions[index].type === 2 && !this.props.campaignOptions[index].lookuped)) {
      //  キャンペーンメンバーに追加しない。 または キャンペーン選択で未選択 なら ステータス選択肢をクリア
      campaignId = null;
    }
    else if (this.props.campaignOptions[index].type === 2 && this.props.campaignOptions[index].lookuped){
      campaignId = this.props.campaignOptions[index].lookuped.value;
    }
    else {
      campaignId = this.props.campaignOptions[index].id;
    }
    // ステータス選択肢を更新
    this.props.setCampaignMemberStatusS(campaignId);
  }

  // goBack() {
  //   this.props.goBack();
  // }

  getRecTypeName(id)  {
    let name = null;
    this.props.recTypes.infos.forEach((item) => {
      if(item.id === id) {
        name = item.label;
      }
    });
    return name;
  }

  //  次へ、 キャンペーンの未選択でも進む
  goOn(event) {
    event.preventDefault();
    let opt = null;
    this.props.campaignOptions.map((item) => {
      if (item.selected) {
        opt = item;
      }
    });
    // if (!opt || (opt.type ===2 && !opt.lookuped)) {
    //   return
    // }
    if (opt && opt.type !== 4)  { // 4:キャンペーンメンバーにしない でない
      opt["status"] = this.props.selectedCampaignMemberStatus;
    }
    this.props.goOn(opt);
  }

  makeContent(values) {
    if (!values)  return null;
    let lines =
    values.map((val, idx) => {
      return <li key={ idx }>{ val }</li>;
    });
    let result =  <ul> { lines } </ul>;
    return result;
  }

  render() {
    if (this.props.loading || !this.props.campaignOptions || !this.props.svconfig) {
      return <div></div>
    }
    const statusLabel = this.props.svconfig.configVersion && this.props.svconfig.configVersion >= 7 ? this.props.svconfig.labels.campaignMember.statusLabel : 'キャンペーンメンバーの状況';    // lx 2.0
    // キャンペーンの ルックアップ
    let lookup;
    lookup = <LookupSObject ref="lookup" sobject="Campaign" onSelectHandler={ this.onLookupChange.bind(this) }
      disabled={ false } selected={ this.state.lookupedCampaign }/>

    // キャンペーンメンバーステータスの選択ComboBox
    let selectStatus;
    if (this.props.campaignMemberStatuss) {
      // 選択肢
      let statusOpts = this.props.campaignMemberStatuss.map((item, n) => {
        let selected = false;
        if (this.props.selectedCampaignMemberStatus && this.props.selectedCampaignMemberStatus.Id === item.Id) {
          selected = true;
        }
        else if (!this.props.selectedCampaignMemberStatus && item.IsDefault) {
          selected = true;
        }
        return <Option key={ n } value={ item.Id } selected={ selected }>{item.Label}</Option>;
      });
      // Combobox
      selectStatus =
        <Form type='inline'>
          <Select ref="recType" className="slds-text-body--regular" label= { null }
            onChange={ this.onChangeStatusSelect.bind(this, 'campaignStatus') }
              disabled={ this.state.selectedInex === -1 || this.props.campaignOptions[this.state.selectedInex].type === 4 } >
          { statusOpts }
          </Select>
        </Form>;
    }
    let opts;
    if (this.props.campaignOptions) {
      opts = this.props.campaignOptions.map((item, idx) => {
      //   return (
      //     <li className="slds-item" key={idx}>
      //       <RegistOption key={ idx } sobjectname="Campaign" registOption={ item } selectHandler={ this.onSelectOption.bind(this, idx) } />
      //     </li>
      //   );
      // });
        let content = item.values ? this.makeContent(item.values) : (item.info ? item.info : "");
        let popover;
        return (
          <li className="slds-item" key={ idx }>
            <div className="slds-tile slds-media" >
              <div className="slds-media__figure" onClick={ this.onSelectOption.bind(this, item.index) } >
                <div className="slds-grid slds-grid--vertical-align-center slds-grid--align-center">
                  <Radio label='' value={ item.index } checked={ item.selected }/>
                </div>
              </div>
              <div className="slds-media__figure" onClick={ this.onSelectOption.bind(this, item.index) } >
                <Icon category='standard' icon="campaign" className='slds-m-right--small'/>
              </div>
              <div className="slds-media__body" >
                <div className="slds-grid slds-nowrap" >
                  <div className="slds-col slds-size--1-of-2 slds-m-horizontal--x-small" onClick={ this.onSelectOption.bind(this, item.index) } >
                    <Popover key={ 'p'+item.index }
                      ref={ ref => (popover = ref) }
                      position='top-left'
                      tooltip
                      theme='info'
                      style={{
                        position: 'absolute',
                        marginTop: +24,
                      }}>
                      <div className="slds-text-body--regular">{ content }</div>
                    </Popover>
                    <h3 className="slds-truncate slds-section__title" title="">
                        <a href="javascript:void(0);" onMouseEnter={ () => popover.toggle(true) } onMouseLeave={ () => popover.toggle(false) }>{ item.title }</a>
                    </h3>
                    <div className="slds-tile__detail slds-text-body--small">
                      <ul className="slds-list--description slds-has-dividers--right">
                        <li className="slds-item">{ item.summary ? item.summary.toString() : "" }</li>
                      </ul>
                    </div>

                  </div>
                  <div className="slds-col slds-size--1-of-2 slds-m-horizontal--x-small">
                    { item.type === 2 ? lookup : null }
                  </div>
                </div>

              </div>
            </div>
          </li>
        );
      });
    }
    // let selectCampaignStatus =
    //   <Form type='inline'>
    //     <Select ref="recType" className="slds-text-body--regular"
    //       label="レコードタイプ"
    //       onChange={ this.onChangeStatusSelect.bind(this, 'recTypeId') }
    //       defaultValue={ defRectype.id } >
    //     {opts}
    //     </Select>
    //   </Form>;


    const required = true;
    const overwriteSecsion = this.props.svconfig.registDispContactOverWriteCheckbox ? classnames("slds-grid slds-grid--vertical-stretch") : classnames("slds-hide");  // 2017.06.xx v1.20, 2018.03.xx v1.22
    const styles = { padding: '12px' };
    return (
      <div style={ styles } className="slds-scrollable">
        {/* キャンペーンの選択 */}
        <RadioGroup label='' name='radiogroup1'>
          <ul className="slds-has-dividers--bottom-space">
            { opts }
          </ul>
        </RadioGroup>
        {/* キャンペーンステータスの選択 */}
        <div className="slds-box slds-theme--shade">
          <div class="slds-form-element">
            <div className="slds-col slds-size--1-of-2 slds-m-horizontal--x-small">
              <label class="slds-form-element__label" for="select-01">{ statusLabel } :</label>
            </div>
            <div className="slds-col slds-size--1-of-2 slds-m-horizontal--x-small">
              { selectStatus }
            </div>
          </div>
        </div>
        {/* フッターの分だけスペースをおく */}
        <div style={{ marginBottom: '48px' }}></div>
        {/* フッター */}
        <Footer left={{ label:"戻る", icon: "back", func: this.props.goBack }}
                center={{ label:"Cancel", icon: "ban", func: this.props.cancel}}
                right={{ label: "次へ", icon: "forward", func: this.goOn.bind(this) }} />
      </div>
    );
  }

}


export default RegistCampaign;



