// RegistDetail.js
// 2017.10.xx v1.21 svconfig追加
import React, { Component, PropTypes } from 'react';
import Lightbox from 'react-image-lightbox';
import classnames from 'classnames';
import Slider from '../slick/slider'

class RegistDetail extends Component {
  constructor(props) {
    super(props);
    this.state = {
      ilbIndex: 0,
      ilbOpen: false
    };
  }

  componentWillMount() {
    this.props.fetchNameCard();
  }

  cancel() {
    this.props.goBack();
  }

  openImage(index) {
    this.setState({ ilbOpen: true && !vfValue.isEdge, ilbIndex: index });
  }

  render() {
    if (this.props.loading || !this.props.card || !this.props.svconfig) { // 2017.10.xx v1.21 svconfig追加
      return <div></div>
    }

    let card  = this.props.card;
    const labels = this.props.svconfig.labels.namecard;   // 2017.10.xx v1.21 名刺関係のラベル

    var settings = {
      dots: false,
      infinite: false,
      speed: 500,
      slidesToShow: 3,
      slidesToScroll: 1,
      initialSlide: 0,
      responsive: [{
        breakpoint: 1360,
        settings: {
          slidesToShow: 3,
          slidesToScroll: 1,
          infinite: true,
          dots: false
        }
      }, {
        breakpoint: 1024,
        settings: {
          slidesToShow: 2,
          dots: true,
          // slidesToScroll: 0,
          // infinite: false,
          // dots: true
        }
      }, {
      //   breakpoint: 600,
      //   settings: {
      //     slidesToShow: 2,
      //     slidesToScroll: 1,
      //     initialSlide: 2
      //   }
      // }, {
        breakpoint: 600, //480,
        settings: {
          slidesToShow: 1,
          dots: true,
          // slidesToScroll: 1,
          // initialSlide: 0,
          // dots: true
        }
      }]
    };

    const styles = { margin: "10px 20px 20px 20px", padding: "0px 20px 0px 20px"};

    // const ilbClass = classnames({"slds-hide" : vfValue.isEdge });     // Edge の場合、Image Lightbox を出さない。 SVGアイコンが表示されないので、

    let dummy_instance = card.dummy_instance ? card.dummy_instance : '';      // ローカルで動かしてるとき用

  　// Lightbox用
    const images = [];
    const {
      ilbIndex,
      ilbOpen,
    } = this.state;

    let front, back;
    if (card.image_id) {
      front = <div><img src={ dummy_instance + '/servlet/servlet.FileDownload?file=' + card.image_id } style={{ padding: "10px", maxHeight: "300px" }} onClick={this.openImage.bind(this, images.length)} /></div>;
      images.push(dummy_instance + '/servlet/servlet.FileDownload?file=' + card.image_id);
    }
    else {
      front = <div><div className="slds-grid slds-grid--align-center slds-m-top--xx-large">表面画像はありません</div></div>;
    }

    if (card.bk_image_id ) {
      back = <div><img src={ dummy_instance + '/servlet/servlet.FileDownload?file=' + card.bk_image_id } style={{ padding: "10px", maxHeight: "300px" }} onClick={this.openImage.bind(this, images.length)} /></div>
      images.push(dummy_instance + '/servlet/servlet.FileDownload?file=' + card.bk_image_id);
    }
    else {
      back = <div><div className="slds-grid slds-grid--align-center slds-m-top--xx-large">裏面画像はありません</div></div>;
    }

    return (
      <div style={ styles }>
        <Slider {...settings}>
          {/*表面*/}
            { front }
          {/*裏面*/}
            { back }
          {/*テキスト情報 royalblue #4169e1 */}
          <div>
            <div className="slds-grid slds-grid--align-center">
              <div className="slds-list--horizontal slds-m-right--xx-large">
                <div className="slds-col"><i className="fa fa-address-card fa-3x" style={{color: "#4169e1"}} aria-hidden="true"></i></div>
                <div className="slds-col slds-p-left--x-large slds-align-middle"><h3 className="slds-section__title">{card.name}</h3></div>
              </div>
            </div>
            <div className="slds-list--horizontal slds-wrap slds-m-top--small">
              <div className="slds-item--label slds-text-color--weak slds-truncate" title={labels.company}>{labels.company}:</div>
              <div className="slds-item--detail slds-truncate">{card.company_name}</div>
              <div className="slds-item--label slds-text-color--weak slds-truncate" title={labels.division}>{labels.division}:</div>
              <div className="slds-item--detail slds-truncate">{card.division}</div>
              <div className="slds-item--label slds-text-color--weak slds-truncate" title={labels.title}>{labels.title}:</div>
              <div className="slds-item--detail slds-truncate">{card.title_name}</div>
              <div className="slds-item--label slds-text-color--weak slds-truncate" title={labels.prefecture}>{labels.prefecture}:</div>
              <div className="slds-item--detail slds-truncate">{card.address_pref}</div>
              <div className="slds-item--label slds-text-color--weak slds-truncate" title={labels.email}>{labels.email}:</div>
              <div className="slds-item--detail slds-truncate">{card.email}</div>
              <div className="slds-item--label slds-text-color--weak slds-truncate" title={labels.mobile}>{labels.mobile}:</div>
              <div className="slds-item--detail slds-truncate">{card.mobile}</div>
              <div className="slds-item--label slds-text-color--weak slds-truncate" title={labels.owner}>{labels.owner}:</div>
              <div className="slds-item--detail slds-truncate">{card.ownerName}</div>
            </div>
          </div>
        </Slider>

        {/* 画像ポップアップ react-image-lightbox zIndex 9001 で SLDS の Footer8000 より前に出す SLDS のModal と背景をあわせるなら Opacity: 0.9 にする
          注意 LEX化だと、iframe で何かしてるので、react-image-lightbo.js の function isInIframe()  で必ず FALSEを返すようにする。*/}
        <div>
          {ilbOpen &&
            <Lightbox
                mainSrc={images[ilbIndex]}
                nextSrc={images[(ilbIndex + 1) % images.length]}
                prevSrc={images[(ilbIndex + images.length - 1) % images.length]}
                reactModalStyle={{overlay: {zIndex: 9001,}, }}
                onCloseRequest={() => this.setState({ ilbOpen: false })}
                onMovePrevRequest={() => this.setState({
                    ilbIndex: (ilbIndex + images.length - 1) % images.length,
                })}
                onMoveNextRequest={() => this.setState({
                    ilbIndex: (ilbIndex + 1) % images.length,
                })}
            />
          }
        </div>

      </div>
    );
  }
}

export default RegistDetail


