import React from 'react';
// import React, { Component } from 'react';
// import { Route, IndexRoute } from 'react-router';
import { browserHistory, Router, Route, IndexRoute, Link } from 'react-router'

import App from './pages/App';
import Regist from './pages/RegistPage';
import RegistAccount from './containers/RegistAccountContainer.js';
import RegistContact from './containers/RegistContactContainer.js';
import RegistCampaign from './containers/RegistCampaignContainer.js';
import RegistConfirm from './containers/RegistConfirmContainer.js';
import RegistFinish from './containers/RegistFinishContainer.js';

const index = '/';

export default (
  <Router history={ browserHistory }>
    <Route path="/" component={ Regist }>
      <IndexRoute component={ RegistAccount }/>
      <Route path="cont" component={ RegistContact } />
      <Route path="camp" component={ RegistCampaign } />
      <Route path="confirm" component={ RegistConfirm } />
      <Route path="fin" component={ RegistFinish } />
    </Route>
  </Router>
);




