// RegistDetailContainer.js
import React, { Component } from 'react';
import { connect } from 'react-redux';
import { fetchNameCard, fetchNameCardSuccess, fetchNameCardFailure } from '../actions/index';
import RegistDetail from '../components/RegistDetail.js';

const mapStateToProps = (state, ownProps) => {
  const { step, loading,  } = state.get('process').toObject();
  const card = state.getIn(['registOptions', 'card']);
  const svconfig = state.getIn(['config', 'svconfig']);   // 2017.10.xx v1.21 svconfig追加
  return { card, step, loading, svconfig, };
}

const mapDispatchToProps = (dispatch, ownProps) => {
  return {
    // 選択した名刺の登録候補一覧の表示
    fetchNameCard: (querySettings) => {
      dispatch(fetchNameCard(vfValue.targetId)).then((response) => {
        return !response.error ? dispatch(fetchNameCardSuccess(response.payload)) : dispatch(fetchNameCardFailure({ ...response.payload, goback:true }));
      });
    },
  }

}

export default connect(mapStateToProps, mapDispatchToProps)(RegistDetail);



