// RegistHeaderContainer.js
import React, { Component } from 'react';
import { connect } from 'react-redux';
// import * as Immutable from 'immutable';
import { changeQuerySettings,
        showModalQuerySettings,
        fetchSVConfig, fetchSVConfigSuccess, fetchSVConfigFailure,
        // fetchUser, fetchUserSuccess, fetchUserFailure,
        fetchRecordTypeInfos, fetchRecordTypeInfosSuccess, fetchRecordTypeInfosFailure,
        fetchAccountOptions, fetchAccountOptionsSuccess, fetchAccountOptionsFailure,
        addError,
      } from '../actions/index';
import RegistHeader from '../components/RegistHeader.js';

const mapStateToProps = (state, ownProps) => {
  const { step, loading, } = state.get('process').toObject();
  const querySettings = state.getIn(['config', 'querySettings']);
  const svconfig = state.getIn(['config', 'svconfig']);
  const res = { svconfig, querySettings, step, loading, };
  return  res;
}

const mapDispatchToProps = (dispatch, ownProps) => {
  return {
    // 設定の取得
    fetchSVConfig : () => {
      dispatch(fetchSVConfig()).then((response) => {
        if (!response.error) {
          dispatch(fetchSVConfigSuccess(response.payload));
          if (!response.payload.enableAccountRegist) {
            // 取引先あるいは取引先責任者のアクセス権がない
            const content = { title: "確認してください", level: "alert", message: "オブジェクトのアクセス権が不足しています。", goback:true };
            dispatch(addError(content));
          }
        }
        else {
          dispatch(fetchSVConfigFailure({ ...response.payload, goback:true }));
        }
      });
    },

    // アカウント、コンタクトとか登録先オブジェクトのレコードタイプ情報の取得
    fetchRecordTypeInfos: (objNames) => {
      dispatch(fetchRecordTypeInfos(objNames)).then((response) => {
        !response.error ? dispatch(fetchRecordTypeInfosSuccess(response.payload)) : dispatch(fetchRecordTypeInfosFailure({ ...response.payload, goback:true }));
      });
    },

    //  クエリ設定モーダルの 　show - hide
    showModalQuerySettings: () => {
      dispatch(showModalQuerySettings(true));
    },
    // changeQuerySettings : (type) => {
    //   dispatch(changeQuerySettings(type));
    // },

    // 選択した名刺の登録候補一覧の表示
    // fetchAccountOptions: (type) => {
    //   let querySettings;
    //   switch(type) {
    //     case 1:
    //       querySettings = {
    //         queryType: 1,
    //         querySite: true,
    //         queryState: false,
    //         queryCity: false,
    //         queryZip: false,
    //       };
    //       break;
    //     case 2:
    //       querySettings = {
    //         queryType: 2,
    //         querySite: true,
    //         queryState: true,
    //         queryCity: false,
    //         queryZip: false,
    //       };
    //       break;
    //     case 3:
    //       querySettings = {
    //         queryType: 3,
    //         querySite: true,
    //         queryState: true,
    //         queryCity: true,
    //         queryZip: false,
    //       };
    //       break;
    //     case 4:
    //       querySettings = {
    //         queryType: 4,
    //         querySite: true,
    //         queryState: false,
    //         queryCity: false,
    //         queryZip: true,
    //       };
    //       break;
    //     default: // 0
    //       querySettings = {
    //         queryType: 0,
    //         querySite: false,
    //         queryState: false,
    //         queryCity: false,
    //         queryZip: false,
    //       };
    //       break;
    //   }
    //   dispatch(changeQuerySettings(querySettings));
    //   // 2018.03.xx v1.22 レコードタイプを指定してなかった。
    //   querySettings.acntRecTypeIds = this.querySettings.acntRecTypeIds;
    //   querySettings.contRecTypeIds = this.querySettings.contRecTypeIds;
    //   dispatch(fetchAccountOptions({ cardId: vfValue.targetId, querySettings })).then((response) => {
    //     // let data = response.payload.data ? response.payload.data : 'Network Error';
    //     return !response.error ? dispatch(fetchAccountOptionsSuccess(response.payload)) : dispatch(fetchAccountOptionsFailure(response.payload));
    //   });
    // },

    // 2018.03.xx v.1.22
    fetchAccountOptions: (querySettings) => {
      dispatch(changeQuerySettings(querySettings));
      dispatch(fetchAccountOptions({ cardId: vfValue.targetId, querySettings })).then((response) => {
        // let data = response.payload.data ? response.payload.data : 'Network Error';
        return !response.error ? dispatch(fetchAccountOptionsSuccess(response.payload)) : dispatch(fetchAccountOptionsFailure(response.payload));
      });
    },

    onChangeView : (id) => {
      dispatch(fetchCards({viewId: id})).then((response) => {
        // let data = response.payload.data ? response.payload.data : 'Network Error';
        return  !response.error ? dispatch(fetchCardsSuccess(response.payload)) : dispatch(fetchCardsFailure(response.payload));
      });
    }

  }
}

export default connect(mapStateToProps, mapDispatchToProps)(RegistHeader);




