// registoptions-reducer.js
//  名刺の登録先候補の一覧
// @flow
import { Map } from 'immutable'
import {
  FETCH_NAMECARD, FETCH_NAMECARD_SUCCESS, FETCH_NAMECARD_FAILURE,
  FETCH_REGISTERS, FETCH_REGISTERS_SUCCESS, FETCH_REGISTERS_FAILURE,
  FETCH_ACCOUNTOPTIONS, FETCH_ACCOUNTOPTIONS_SUCCESS, FETCH_ACCOUNTOPTIONS_FAILURE,
  FETCH_CONTACTOPTIONS, FETCH_CONTACTOPTIONS_SUCCESS, FETCH_CONTACTOPTIONS_FAILURE,
  FETCH_CAMPAIGNOPTIONS, FETCH_CAMPAIGNOPTIONS_SUCCESS, FETCH_CAMPAIGNOPTIONS_FAILURE, // lx 2.0
  FETCH_CAMPAIGNSTATUSLIST, FETCH_CAMPAIGNSTATUSLIST_SUCCESS, FETCH_CAMPAIGNSTATUSLIST_FAILURE, // lx 2.0
  FETCH_CAMPAIGNMEMBERSTATUSLIST, FETCH_CAMPAIGNMEMBERSTATUSLIST_SUCCESS, FETCH_CAMPAIGNMEMBERSTATUSLIST_FAILURE, // lx 2.0
  SELECT_ACCOUNT_OPTION, SELECT_CONTACT_OPTION,
  SELECT_CAMPAIGN_OPTION, SELECT_CAMPAIGNMEMBERSTATUS,    // lx 2.0
  FETCH_RECORDTYPES_SUCCESS,
  SELECT_ACCOUNT_LOOKUPED, SELECT_CONTACT_LOOKUPED,
  CHANGE_REGISTTARGET, // lx 2.0
  SELECT_LEAD_LOOKUPED, // lx 2.0
  SELECT_CAMPAIGN_LOOKUPED, // lx 2.0
  SELECT_ACCOUNT_RECTYPE, SELECT_CONTACT_RECTYPE, SELECT_LEAD_RECTYPE,
} from '../actions/index';

const INITIAL_STATE = Map({
  card: null,
  // registTarget: 0,      // 登録先候補の表示状態 0: 両方、1: アカウント、2:リード
  accountOptions: [], //accountLookuped: null, accountRectypeId: null,
  contactOptions: [], //contactLookuped: null, contactRectypeId: null,
  campaignOptions: [],
  campaignStatuss: [],
  campaignMemberStatuss: [],
  selectedCampaignStatus: null,
  accountDefRecType: null,
  contactDefRecType: null,
  leadDefRecType: null,    // lx 2.0 2018.07.xx
  error: null, loading: false,
});

export default (state: Object = INITIAL_STATE, action: Object) => {
  switch(action.type) {

  // 選択した名刺の情報
  case FETCH_NAMECARD:
    return state.merge(Map({ card: null, loading: true, error: null }));
  case FETCH_NAMECARD_SUCCESS:
    return state.merge(Map({ card: action.payload, loading: false, error: null }));
  case FETCH_NAMECARD_FAILURE:
    return state.merge(Map({ card: null, loading: false, error: action.payload }));

  // // 登録先候補 オブジェクトの選択
  // case CHANGE_REGISTTARGET:
  //   return state.merge(Map({ registTarget: action.payload }));

  // 名刺ごとの登録先候補
  case FETCH_REGISTERS:
    return state.merge(Map({ registers:[], error: null, loading: true }));
  case FETCH_REGISTERS_SUCCESS:
    return state.merge(Map({ registers: action.payload, error:null, loading: false }));
  case FETCH_REGISTERS_FAILURE:
    return state.merge(Map({ registers: null, error: action.payload, loading: false }));

  // アカウント、リード 登録オプション
  case FETCH_ACCOUNTOPTIONS:
    return state.merge(Map({ 'accountOptions': [], loading: true, error: null }));
  case FETCH_ACCOUNTOPTIONS_SUCCESS:
  {
    let accountOptions =  action.payload;
    let acntDefRecType = state.get('accountDefRecType');
    let leadDefRecType = state.get('leadDefRecType');
    // let registTarget = state.get('registTarget');
    // let acnt = false;
    // let lead = false;
    accountOptions.forEach((item, idx) => {
      // if (!acnt && item.otype === 'Account') {
      //   acnt = true;
      // }
      // if (!lead && item.otype === 'Lead') {
      //   lead = true;
      // }
      if (item.type===3) { // 新規作成
        if (item.otype === 'Account' && acntDefRecType !== null) {
          accountOptions.splice(idx, 1, Object.assign({}, item, {recTypeId: acntDefRecType.id, recTypeName: acntDefRecType.name}));
        }
        else if (item.otype === 'Lead' && leadDefRecType !== null) {
          accountOptions.splice(idx, 1, Object.assign({}, item, {recTypeId: leadDefRecType.id, recTypeName: leadDefRecType.name}));
        }
      }
    });
    // if (acnt) {
    //   registTarget = lead ? 0 : 1;
    // }
    // else if (lead) {
    //   registTarget = 2;
    // }
    // return state.merge(Map({ 'accountOptions': accountOptions, 'registTarget' : registTarget, loading: false, error: null }));
    return state.merge(Map({ 'accountOptions': accountOptions, loading: false, error: null }));
  }
  case FETCH_ACCOUNTOPTIONS_FAILURE:
    return state.merge(Map({ 'accountOptions': null, loading: false, error: action.payload }));

  // コンタクト 登録オプション
  case FETCH_CONTACTOPTIONS:
    return state.merge(Map({ contactOptions: [], loading: true, error: null }));
  case FETCH_CONTACTOPTIONS_SUCCESS:
  {
    let contactOptions =  action.payload;
    let defRecType = state.get('contactDefRecType');
    if ( defRecType !== null) {
      contactOptions.forEach((item, idx) => {
        if (item.type===3) {
          contactOptions.splice(idx, 1, Object.assign({}, item, {recTypeId: defRecType.id, recTypeName: defRecType.name}));
        }
      });
    }
    return state.merge(Map({ contactOptions: contactOptions, loading: false, error: null }));
  }
  case FETCH_CONTACTOPTIONS_FAILURE:
    return state.merge(Map({ contactOptions: null, loading: false, error: action.payload }));

  case FETCH_CAMPAIGNOPTIONS:   // lx 2.0
    return state.merge(Map({ campaignOptions: [], loading: true, error: null }));
  case FETCH_CAMPAIGNOPTIONS_SUCCESS:
    return state.merge(Map({ campaignOptions: action.payload, loading: false, error: null }));
  case FETCH_CAMPAIGNOPTIONS_FAILURE:
    return state.merge(Map({ campaignOptions: null, loading: false, error: action.payload }));

  case FETCH_CAMPAIGNSTATUSLIST: // lx 2.0
    return state.merge(Map({ campaignStatuss: [], loading: true, error: null }));
  case FETCH_CAMPAIGNSTATUSLIST_SUCCESS:
    return state.merge(Map({ campaignStatuss: action.payload, loading: false, error: null }));
  case FETCH_CAMPAIGNSTATUSLIST_FAILURE:
    return state.merge(Map({ campaignStatuss: null, loading: false, error: action.payload }));

  case FETCH_CAMPAIGNMEMBERSTATUSLIST: // lx 2.0
    return state.merge(Map({ campaignMemberStatuss: [], loading: true, error: null }));
  case FETCH_CAMPAIGNMEMBERSTATUSLIST_SUCCESS:
    return state.merge(Map({ campaignMemberStatuss: action.payload, loading: false, error: null }));
  case FETCH_CAMPAIGNMEMBERSTATUSLIST_FAILURE:
    return state.merge(Map({ campaignMemberStatuss: null, loading: false, error: action.payload }));

  case SELECT_ACCOUNT_OPTION:
  case SELECT_CONTACT_OPTION:
  {
    let label = action.type === SELECT_ACCOUNT_OPTION ? 'accountOptions' : 'contactOptions';
    return state.update(label, (item) => {
      return item.map((val, idx) => {
        val.selected = (val.index === action.payload);
        return val;
      });
    });
  }
  case SELECT_ACCOUNT_LOOKUPED:
  case SELECT_CONTACT_LOOKUPED:
  case SELECT_LEAD_LOOKUPED:   // lx 2.0
  case SELECT_CAMPAIGN_LOOKUPED:   // lx 2.0
  {
    // label = action.type === SELECT_ACCOUNT_LOOKUPED ? 'accountOptions' : 'contactOptions';
    let label = '';
    let otype = ''; // accountOptions に Accoun と Lead とあるのを区別するよう
    if (action.type === SELECT_ACCOUNT_LOOKUPED) {
      label = 'accountOptions';
      otype = 'Account';
    }
    else if (action.type === SELECT_CONTACT_LOOKUPED) {
      label = 'contactOptions';
      otype = 'Contact';
    }
    else  if (action.type === SELECT_LEAD_LOOKUPED) {
      label = 'accountOptions';
      otype = 'Lead';
    }
    else  if (action.type === SELECT_CAMPAIGN_LOOKUPED) {
      label = 'campaignOptions';
      otype = 'Campaign';
    }
    return state.update(label, (item) => {
      return item.map((val, idx) => {
        if (val.type === 2 && val.otype === otype) {
          val['lookuped'] = action.payload;
        }
        return val;
      });
    });
  }
  //
  case SELECT_CAMPAIGN_OPTION: // lx 2.0
  {
    return state.update('campaignOptions', (item) => {
      return item.map((val, idx) => {
        val.selected = (val.index === action.payload);
        return val;
      });
    });
  }

  case SELECT_CAMPAIGNMEMBERSTATUS: // lx 2.0
  {
    return state.update("selectedCampaignMemberStatus", (item) => {
      item= action.payload;
      return item;
    });
  }

  // レコードタイプ
  // デフォルトは一番目に入ってる。
  case FETCH_RECORDTYPES_SUCCESS:
  {
    let accountRecType = action.payload.Account && action.payload.Account.infos ?
        action.payload.Account.infos[0] : null;
    let contactRecType = action.payload.Contact && action.payload.Contact.infos ?
        action.payload.Contact.infos[0] : null;
    let leadRecType = action.payload.Lead && action.payload.Lead.infos ?
        action.payload.Lead.infos[0] : null;
    return state.update((value) => {
      value.forEach((item, label) => {
        if (accountRecType && label ==='accountDefRecType') {
          item = {
            id: accountRecType.id,
            name: accountRecType.label,
          };
        }
        else if (contactRecType && label ==='contactDefRecType') {
          item = {
            id: contactRecType.id,
            name: contactRecType.label,
          }
        }
        else if (leadRecType && label ==='leadDefRecType') {
          item = {
            id: leadRecType.id,
            name: leadRecType.label,
          }
        }
        value = value.set(label, item);
      });
      return value;
    });
  }
  //  新規作成時のレコードタイプの選択
  case SELECT_ACCOUNT_RECTYPE:
  case SELECT_CONTACT_RECTYPE:
  case SELECT_LEAD_RECTYPE:
  {
    let label = '';
    let otype = ''; // accountOptions に Accoun と Lead とあるのを区別するよう
    if (action.type === SELECT_ACCOUNT_RECTYPE) {
      label = 'accountOptions';
      otype = 'Account';
    }
    else if (action.type === SELECT_CONTACT_RECTYPE) {
      label = 'contactOptions';
      otype = 'Contact';
    }
    else  if (action.type === SELECT_LEAD_RECTYPE) {
      label = 'accountOptions';
      otype = 'Lead';
    }
    return state.update(label, (item) => {
      return item.map((val, idx) => {
        if (val.type === 3 && val.otype === otype) {
          val['recTypeId'] = action.payload.id;
          val['recTypeName'] = action.payload.name;
        }
        return val;
      });
    });
  }
  default:
    return state;
  }
}



