//
import React, { Component } from 'react';
import RegistHeader from '../containers/RegistHeaderContainer.js';
import RegistDetail from '../containers/RegistDetailContainer.js';
import RegistProcess from '../containers/RegistProcessContainer.js';
import Loading from '../containers/LoadingContainer.js';

const RegistPage = ({ children, location }) => (
      <div className='container'>
        <Loading/>
        <div style={{ position: "fixed", top: "0px", width: "100%", zIndex:"9001" }} >
          <RegistHeader/>
        </div>
        <div style={{ marginTop: "100px" }}>
          <RegistDetail/>
          <RegistProcess location={ location } />
          { children }
        </div>
      </div>
)


export default RegistPage;



